.module-accordion {
    padding: pxToRem(17) 0;

    &__title {
        color: $base-font-colour;
        font-size: pxToRem(25);
        margin: 0;
    }

    &__list {
        @include sq-list-reset;
        margin-top: pxToRem(19);
        text-align: left;
    }

    &__item {
        @include sq-border-radius(10 + px);
        background-color: $white;
        margin-bottom: pxToRem(16);
        overflow: hidden;

        &:first-child {
            border: none;
        }

        &:hover,
        &:focus,
        &.active {
            .module-accordion__link {
                background-color: $white;
                color: $base-font-colour;
            }
        }

        &.active {
            .module-accordion__link {
                &::before {
                    @include sq-opacity(0);
                }

                &::after {
                    @include sq-opacity(100);
                    @include sq-transform(rotate(0deg));
                    background-color: $black;
                }
            }
            .module-accordion__content {
                max-height: pxToRem(800);
                visibility: visible;
                @include sq-transition(
                    all 0.5s cubic-bezier(0.87, 0.08, 0.87, 0.08)
                );
            }
        }
    }

    &__link {
        border: none;
        color: $gray;
        display: block;
        font-size: pxToRem(18);
        font-weight: $font-weight-bold;
        padding: pxToRem(26) 80px pxToRem(26) pxToRem(20);
        position: relative;
        text-decoration: none;
        z-index: 1;
        @include sq-transition(background-color 0.5s ease, color 0.5s ease);

        &:focus,
        &:hover {
            background-color: $white;
            color: $gray;
        }

        &:focus {
            outline-offset: -4px;
        }

        &::before,
        &::after {
            @include sq-border-radius(2px);
            background-color: $accordion-btn-bg;
            bottom: 0;
            content: '';
            height: 4px;
            margin: auto 0;
            position: absolute;
            right: 30px;
            top: 0;
            width: 20px;
            z-index: 2;
        }

        &::after {
            @include sq-transition(transform 0.4s ease);
            @include sq-transform(rotate(90deg));
        }

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(20);
            padding: pxToRem(25) 80px pxToRem(25) pxToRem(30);
        }
    }

    &__content {
        @include sq-transition(
            max-height 0.5s cubic-bezier(0.08, 0.87, 0.08, 0.87)
        );
        font-size: pxToRem(15);
        font-weight: $font-weight-light;
        line-height: pxToRem(24);
        max-height: 0;
        overflow: hidden;
        padding: 0;

        p {
            margin: 0;
        }
    }

    &__content-wrapper {
        overflow: hidden;
        padding: 0 pxToRem(20) pxToRem(20);

        @include rwd-from($breakpoint-desktop-min) {
            padding: 0 pxToRem(30) pxToRem(30) pxToRem(30);
        }

        &.text-wrapper {
            max-width: inherit;
        }
    }
}
