.header-logo {
    display: inline-block;
    vertical-align: middle;

    @include rwd-from($breakpoint-desktop-min) {
        float: left;
    }

    h1 {
        line-height: 0;
        margin: 0;
    }

    &__link {
        display: inline-block;
        margin: 0 pxToRem(15) 0 0;
        width: pxToRem(105);

        @include rwd-from($breakpoint-desktop-min) {
            width: pxToRem(137);
        }
    }

    &__img {
        width: 100%;
    }
}
