.quick-view {
    $self: '.quick-view';
    background-color: $white;
    bottom: 0;
    box-shadow: -8px 0 20px rgba(0, 0, 0, 0.08);
    position: fixed;
    right: -85%;
    top: 0;
    transition: right .4s ease 0s; /* This is the "slide in/out" effect */
    width: 85%;
    z-index: 4;
    margin-right: 0;
    border: none;

    &[open] {
        right: 0;
        margin-top: 0;
        max-height: 100%;
        padding-right: 0;
        overflow-y: hidden; // to hide scroll bar, a child element is used for scrolling

        #{ $self }__close {
            right: 0;
            transition: right 0.2s ease 0.2s;
            z-index: 5;
        }
    }

    &:not([open]) {
        /**
         * Intentionally override useragent rule of display: none;
         * 
         * When the display type changes, transitions are not performed
         * by the browser. For example, changing from `display: hidden;` to
         * `display: block;` will not execute the "slide in" and "slide out"
         * transitions we want for the QuickView.
         * 
         * Therefore, use display: block; so that the transition for the 
         * `right` property is executed.
         */ 
        display: block;
    }

    &:focus {
        outline: none;
    }

    @include rwd-from($search-results-breakpoint-1) {
        width: 70%;
    }

    @include rwd-from($breakpoint-desktop-min) {
        width: 49%;
    }

    &__wrapper {
        height: 100vh;
        overflow: auto;
    }

    &__content {
        padding: pxToRem(70) pxToRem($content-wrapper-padding-mobile) 0 pxToRem(40);
        position: relative;

        @include rwd-from($breakpoint-desktop-min) {
            padding: pxToRem(100) pxToRem($content-wrapper-padding) 0 pxToRem(71);
        }

        @include rwd-to($breakpoint-desktop-min) {
            padding-left: .5rem;
            padding-right: 1rem;    
        }
    }

    &__close {
        background-color: $base-color-lead;
        border: 0;
        cursor: pointer;
        height: pxToRem(52);
        position: fixed;
        right: pxToRem(-55);
        top: 0;
        transition: right 0.1s ease;
        width: pxToRem(52);
        

        @include rwd-from($search-results-breakpoint-1) {
            height: pxToRem(72);
            right: pxToRem(-75);
            width: pxToRem(72);
        }

        &::before {
            background-color: $white;
            bottom: 0;
            content: '';
            height: 3px;
            margin: auto 0;
            position: absolute;
            right: pxToRem(17);
            top: 0;
            transform: rotate(50deg);
            transition: right 0.4s ease;
            width: pxToRem(18);

            @include rwd-from($search-results-breakpoint-1) {
                right: pxToRem(25);
            }
        }

        &::after {
            background-color: $white;
            bottom: 0;
            content: '';
            height: 3px;
            margin: auto 0;
            position: absolute;
            right: pxToRem(17);
            top: 0;
            transform: rotate(-50deg);
            transition: right 0.4s ease;
            width: pxToRem(18);

            @include rwd-from($search-results-breakpoint-1) {
                right: pxToRem(25);
            }
        }
    }

    &__title {
        color: $base-font-colour;
        font-size: pxToRem(23);
        margin-top: 0;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(30);
        }
    }

    &__desc {
        font-size: pxToRem(15);
        font-weight: $font-weight-light;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(18);
        }
    }

    &__details {
        margin-top: pxToRem(40);

        @include rwd-from($search-results-breakpoint-1) {
            margin-top: pxToRem(60);
        }

        &-title {
            color: $base-font-colour;
            font-size: pxToRem(15);
            font-weight: $font-weight-medium;
        }

        &-content {
            background-color: rgba(0, 0, 0, 0.1);
            padding: pxToRem(42) pxToRem(20) pxToRem(42) pxToRem(46);
            position: relative;

            &::before {
                background-color: $base-color-lead;
                bottom: pxToRem(38);
                content: '';
                left: pxToRem(26);
                position: absolute;
                top: pxToRem(42);
                width: 3px;

                @include rwd-from($search-results-breakpoint-1) {
                    bottom: pxToRem(48);
                    left: pxToRem(46);
                    top: pxToRem(52);
                }
            }

            @include rwd-from($search-results-breakpoint-1) {
                padding: pxToRem(52) pxToRem(49) pxToRem(52) pxToRem(76);
            }

            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {

                &:first-child {
                    font-size: pxToRem(16);
                    margin-top: 0;

                    @include rwd-from($search-results-breakpoint-1) {
                        font-size: pxToRem(18);
                    }
                }
            }

            p {
                font-size: pxToRem(14);
                font-weight: $font-weight-light;

                @include rwd-from($search-results-breakpoint-1) {
                    font-size: pxToRem(15);
                }
            }

            dl {
                margin-bottom: pxToRem(20);

                &::after {
                    clear: both;
                    content: '';
                    display: block;
                    height: 0;
                    visibility: hidden;
                }
            }

            dt {
                clear: left;
                float: left;
                font-size: pxToRem(15);
                font-weight: $font-weight-bold;
                padding: pxToRem(1) 0 pxToRem(3) 0;

                @include rwd-from($search-results-breakpoint-1) {
                    font-size: pxToRem(18);
                    padding: pxToRem(10) 0;
                }
            }

            dd {
                float: left;
                font-size: pxToRem(15);
                font-weight: $font-weight-light;
                margin-left: pxToRem(10);
                padding: pxToRem(1) 0 pxToRem(10) 0;

                @include rwd-from($search-results-breakpoint-1) {
                    font-size: pxToRem(18);
                    padding: pxToRem(10) 0;
                }
            }
        }
    }

    /* Overlay on top of all elements */
    @at-root {
        body > .overlay {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: rgba(0, 0, 0, 0.4);
            z-index: 2;
            color: white;
            opacity: 0;
            visibility: hidden;
            transition: opacity .4s ease 0s, visibility 0s;
        }

        /* Display the overlay when the dialog is open */
        .opened-view > .overlay {
            visibility: visible;
            opacity: 1;
        }   
    }      
}

.related {
    $self: '.related';
    background-color: rgba(0,0,0,0.02);
    margin: pxToRem(20) 0 pxToRem(40) 0;
    padding-top: pxToRem(30);

    @include rwd-from($search-results-breakpoint-1) {
        margin-top: pxToRem(30);
        padding: pxToRem(35) 0 pxToRem(10) 0;
    }

    &__title {
        color: $base-font-colour;
        font-size: pxToRem(15);
        margin-top: 0;
        padding-left: pxToRem(40);

        @include rwd-from($search-results-breakpoint-1) {
            padding-left: pxToRem(71);
        }
    }

    &__list {
        @extend %list-reset;
        overflow: auto;
        padding: 0 pxToRem(40);
        scroll-padding: 0 0 0 pxToRem(40);
        scroll-snap-type: x mandatory;
        white-space: nowrap;

        @include rwd-from($search-results-breakpoint-1) {
            min-height: pxToRem(330);
            padding: 0 pxToRem(71);
            scroll-padding: 0 0 0 pxToRem(71);
        }
    }

    &__item {
        background-color: $white;
        border-radius: 5px;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
        display: inline-block;
        margin: 0 pxToRem(17) pxToRem(20) 0;
        overflow: hidden;
        scroll-snap-align: start;
        vertical-align: top;
        white-space: normal;
        width: pxToRem(229);

        &-title {
            font-size: pxToRem(15);
            margin: 0;
        }
    }

    &__desc {
        font-size: pxToRem(14);
        margin: 0;
        max-height: none;
        overflow: hidden;
        padding-top: pxToRem(12);
        transition: all 0.4s ease-in-out;

        @include rwd-from($breakpoint-desktop-min) {
            max-height: 0;
            opacity: 0;
            padding-top: 0;
        }
    }

    &__content {
        min-height: pxToRem(93);
        padding: pxToRem(25) pxToRem(20);
    }

    &__link {
        color: $base-font-colour;
        display: block;
        padding-right: pxToRem(20);
        position: relative;
        text-decoration: none;
        width: 100%;

        @include rwd-from($breakpoint-desktop-min) {

            &::after {
                background: url('../files/arrow-red.svg') no-repeat;
                background-size: 100%;
                content: '';
                height: pxToRem(9);
                position: absolute;
                right: 0;
                top: pxToRem(6);
                transition: transform 0.4s ease;
                width: pxToRem(14);
            }

            &:hover {
                &::after {
                    transform: rotate(180deg);
                }

                + #{ $self }__desc {
                    max-height: pxToRem(300);
                    opacity: 1;
                    padding-top: pxToRem(20);
                }
            }
        }

        span {
            font-weight: $font-weight-light;
        }
    }

    &__bg {
        background-color: $light-gray;
        background-size: cover;
        height: pxToRem(138);
        margin: 0;
        width: 100%;

        img {
            height: pxToRem(138);
            object-fit: cover;
            width: 100%;
        }
    }  
}



