.module-az {
    $self: '.module-az';
    border: 1px solid $black;
    margin: 0 0 pxToRem(37) 0;
    padding: pxToRem(24) pxToRem(29) pxToRem(14) pxToRem(20);
    text-align: center;

    @include rwd-from($search-results-breakpoint-1) {
        text-align: left;
    }

    @include rwd-from($breakpoint-desktop-min) {
        padding: pxToRem(44) pxToRem(66) pxToRem(34) pxToRem(80);
    }

    &__list {
        @extend %list-reset;
        list-style-type: none;
        text-align: center;

        @include rwd-from($breakpoint-desktop-min) {
            text-align: left;
        }
    }

    &__item {
        display: inline-block;
        font-size: pxToRem(20);
        font-weight: $font-weight-regular;
        height: pxToRem(45);
        list-style-type: none;
        margin: 0 pxToRem(3) pxToRem(10) pxToRem(3);
        vertical-align: bottom;
        width: pxToRem(45);

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(25);
            height: pxToRem(55);
            margin: 0 pxToRem(6) pxToRem(10) 0;
            width: pxToRem(55);
        }

        > span {
            align-items: center;
            border: 1px solid $black;
            display: flex;
            height: 100%;
            justify-content: center;
            opacity: 0.3;
        }

        &--btn {
            background-color: $black;
            font-size: pxToRem(18);
            width: auto;
        }
    }

    &__item.active #{ $self }__link {
        background-color: $black;
        color: $white;
    }

    &__item--btn #{ $self }__link {
        color: $white;
        padding: 0 pxToRem(27) 0 pxToRem(27);

        @include rwd-from($breakpoint-desktop-min) {

            &:hover {
                background-color: $body-bg;
                color: $black;
            }
        }
    }

    &__link {
        align-items: center;
        border: 1px solid $black;
        color: $black;
        display: flex;
        height: 100%;
        justify-content: center;
        text-decoration: none;
        transition: background-color 0.3s ease, color 0.3s ease;

        @include rwd-from($breakpoint-desktop-min) {

            &:hover {
                background-color: $black;
                color: $white;
            }
        }
    }

    &__filter {
        border-bottom: 1px solid $black;
        font-size: pxToRem(24);
        margin: 0 pxToRem(-29) pxToRem(24) pxToRem(-20);
        padding: 0 pxToRem(29) pxToRem(24) pxToRem(20);

        @include rwd-from($breakpoint-desktop-min) {
            margin: 0 pxToRem(-66) pxToRem(44) pxToRem(-80);
            padding: 0 pxToRem(66) pxToRem(40) pxToRem(80);
        }
    }
}
