.contact {
    border-top: 1px solid $light-gray;
    margin-top: pxToRem(10);
    overflow: hidden;
    padding: pxToRem(11) 0 0 0;
    width: 100%;

    &__list {
        @extend %list-reset;
        display: block;

        @include rwd-from($breakpoint-desktop-min) {
            display: inline-block;
        }
    }

    &__item {
        color: $contact-color-font;
        display: block;
        font-size: pxToRem(15);
        font-weight: $font-weight-light;
        padding: pxToRem(4) 0;

        @include rwd-from($breakpoint-desktop-min) {
            margin-right: pxToRem(50);
        }

        time {
            display: inline-block;
            vertical-align: middle;
        }

        span {
            display: inline-block;
            vertical-align: middle;
        }

        &--icon {

            &::before {
                background-position: center top;
                background-repeat: no-repeat;
                content: '';
                display: inline-block;
                vertical-align: middle;
                width: pxToRem(20);
            }

            @include tabs-icons('&-envelope', $icon-envelope,16,14,$base-color-lead,$base-color-lead);
            @include tabs-icons('&-phone', $icon-phone,15,15,$base-color-lead,$base-color-lead);
            @include tabs-icons('&-location', $icon-location,14,18,$base-color-lead,$base-color-lead);
            @include tabs-icons('&-clock', $icon-clock,13,13,$base-color-lead,$base-color-lead);

            @at-root .fb-container--edu#{&} {
                @include tabs-icons('&-envelope', $icon-envelope,16,14,$base-color-higher-education,$base-color-higher-education);
                @include tabs-icons('&-phone', $icon-phone,15,15,$base-color-higher-education,$base-color-higher-education);
                @include tabs-icons('&-location', $icon-location,14,18,$base-color-higher-education,$base-color-higher-education);
                @include tabs-icons('&-clock', $icon-clock,13,13,$base-color-higher-education,$base-color-higher-education);
            }
        }

        &--event-time {
            display: block;

            @include rwd-from($breakpoint-desktop-min) {
                display: inline-block;
            }
        }
    }

    &__link {
        color: $contact-color-font;
        font-size: pxToRem(15);
        text-decoration: none;

        @include rwd-from($breakpoint-desktop-min) {

            &:hover {
                text-decoration: underline;
            }
        }
    }
}
