.tools {
    display: inline-block;
    float: right;

    &__print {
        display: none;

        @include rwd-from($breakpoint-desktop-min) {
            color: $base-font-colour;
            display: inline-block;
            font-size: pxToRem(20);
            padding-right: pxToRem(45);
            position: relative;
            text-decoration: none;

            &:hover {
                text-decoration: underline;
            }

            &::after {
                background: url('../files/print.svg') no-repeat;
                background-size: 100%;
                content: '';
                height: pxToRem(28);
                position: absolute;
                right: 0;
                width: pxToRem(28);
            }
        }
    }
}
