.tags {
    clear: left;
    margin-top: pxToRem(10);

    @include rwd-from($breakpoint-desktop-min) {
        margin-top: 0;
    }

    &__list {
        @extend %list-reset;
    }

    &__item {
        background-color: $tags-bg;
        border-radius: 32px;
        color: $base-color-lead;
        display: inline-block;
        font-size: pxToRem(15);
        font-weight: $font-weight-regular;
        margin: 0 pxToRem(5) pxToRem(10) 0;
        padding: pxToRem(6) pxToRem(15);

        @at-root .fb-container--edu#{&} {
            color: $base-color-higher-education;
        }
    }
}
