.dropdown-list {
    display: block;
    position: relative;

    @include rwd-from($search-results-breakpoint-1) {
        display: inline-block;
    }

    &__link {
        background-color: transparent;
        border: 0;
        border-bottom: 1px solid rgba(0,0,0,0.5);
        color: rgba(0,0,0,0.5);
        cursor: pointer;
        margin: 0 pxToRem(8);
        padding: 0 pxToRem(40) 0 0;
        position: relative;
        text-decoration: none;
        transition: text-shadow 0.4s ease;

        &::after {
            background: url('../files/arrow-gray.svg') no-repeat;
            background-size: 100%;
            bottom: 0;
            content: '';
            display: inline-block;
            height: pxToEm(8);
            margin: auto 0;
            opacity: 0.9;
            position: absolute;
            right: pxToRem(5);
            top: 0;
            transition: transform 0.4s ease;
            width: pxToEm(13);
        }

        &:focus {
            outline: none;
            text-shadow: 2px 6px 10px $light-gray;
        }

        @include rwd-from($breakpoint-desktop-min) {

            &:hover {
                text-shadow: 2px 6px 10px $light-gray;
            }
        }

        &.opened {

            &::after {
                transform: rotate(180deg);
            }
        }
    }

    &__list {
        @extend %list-reset;
        display: none;
        max-height: pxToRem(235);
        min-width: pxToRem(210);
        overflow: auto;
        padding: pxToRem(20) 0;
        position: relative;
        width: 100%;
        z-index: 2;

        &::after {
            display: none;
        }

        @include rwd-from($search-results-breakpoint-1) {
            background-color: $white;
            left: calc( (100% - #{pxToRem(210)}) / 2 );
            position: absolute;
            top: pxToRem(40);
        }

        &-link {
            color: $base-font-colour;
            display: block;
            font-size: pxToRem(16);
            padding: pxToRem(5) pxToRem(30);
            text-decoration: none;

            @include rwd-from($breakpoint-desktop-min) {

                &:hover {
                    background-color: $dropdown-link-hover;
                }

                &:focus {
                    outline-offset: -3px;
                }
            }
        }
    }
}
