.footer-links {
    display: block;
    margin-bottom: pxToRem(20);
    padding-right: 0;
    vertical-align: top;

    @include rwd-from($breakpoint-desktop-min) {
        display: inline-block;
        margin-bottom: 0;
        padding-right: pxToRem(120);
    }

    &__title {
        font-size: pxToRem(16);
        margin: 0 0 pxToRem(10) 0;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(20);
            margin: 0 0 pxToRem(32) 0;
        }
    }

    &__list {
        @extend %list-reset;

        @include rwd-from($breakpoint-desktop-min) {
            display: block;
        }
    }

    &__item {

        &--icon {
            display: inline-block;
            padding-right: pxToRem(14);
        }
    }

    &__item--icon &__link {
        display: block;
        height: pxToRem(37);
        padding: 0;
        width: pxToRem(37);
    }

    &__link {
        color: $white;
        display: inline-block;
        font-size: pxToRem(14);
        padding: pxToRem(4) 0;
        text-decoration: none;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(16);

            &:hover {
                text-decoration: underline;
            }
        }
    }
}
