.module-intro {


    @include rwd-from($breakpoint-desktop-min) {
        display: flex;
        margin: pxToRem(100) auto  pxToRem(130) auto;
    }

    &__title {
        color: $black;
        font-size: pxToRem(30);
        margin: pxToRem(11) 0;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(69);
            line-height: pxToRem(87);
            margin: 0;
        }
    }

    &__desc {
        color: $base-color-lead;
        font-size: pxToRem(16);
        font-style: italic;
        font-weight: $font-weight-medium;
        line-height: pxToRem(26);
        margin: pxToRem(11) 0;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(33);
            line-height: pxToRem(53);
            margin: 0;
            padding: 0 pxToRem(130) 0 pxToRem(150);
        }
    }
}
