.header-hamburger {
    display: inline-block;
    float: right;
    height: pxToRem(24);
    padding: 0 0 pxToRem(2) pxToRem(10);
    position: relative;
    transform: scale(0.9);
    width: calc( #{$bar-width} + #{pxToRem(10)});

    @include rwd-from($breakpoint-desktop-min) {
        border-left: 1px solid $bar-border-color;
        height: pxToRem(38);
        padding: pxToRem(2) 0 pxToRem(2) pxToRem(25);
        transform: scale(1);
        width: calc( #{$bar-width} + #{pxToRem(25)});
    }

    &__button {
        bottom: 0;
        display: inline-block;
        height: $bar-height;
        margin: auto 0;
        position: absolute;
        right: 0;
        top: 0;
        width: $bar-width;

        > span {
            background: $bar-bg;
            border-radius: 3px;
            display: block;
            height: 3px;
            position: absolute;
            top: $bar-spacing;
            width: $bar-width;
            @at-root .is-active#{&},
            .nav-show#{&} {
                background: none;
            }
            &::before,
            &::after {
                background-color: $bar-bg;
                border-radius: 3px;
                content: '';
                display: block;
                height: 3px;
                left: 0;
                position: absolute;
                transition: $animation-time ease;
                width: $bar-width;
            }
            &::before {
                top: -$bar-spacing;
                @at-root .is-active#{&},
                .nav-show#{&} {
                    background-color: $bar-bg;
                    top: 0;
                    transform: rotate(45deg);
                }

                @at-root .nav-show#{&} {
                    background-color: $bar-bg;
                }
            }
            &::after {
                bottom: -$bar-spacing;
                @at-root .is-active#{&},
                .nav-show#{&} {
                    background-color: $bar-bg;
                    bottom: 0;
                    transform: rotate(-45deg);
                }

                @at-root .nav-show#{&} {
                    background-color: $bar-bg;
                }
            }
        }
    }
}
