.header-top-links {
    display: flex;
    vertical-align: middle;

    &__list {
        @extend %list-reset;
        margin: 0 pxToRem(-20);

        &:last-child {
            margin-left: auto;
        }
        &:only-child {
            margin-left: pxToRem(-20);
        }

        @include rwd-from($breakpoint-desktop-min) {
            display: block;
        }
    }

    &__item {
        display: inline-block;
    }

    &__link {
        color: $white;
        font-size: pxToRem(15);
        font-weight: $font-weight-light;
        padding: 0 pxToRem(20);
        text-decoration: none;

        @include rwd-from($breakpoint-desktop-min) {

            &:hover {
                text-decoration: underline;
            }
        }

        &--icon {

            &::before {
                background-position: center;
                background-repeat: no-repeat;
                content: '';
                display: inline-block;
                margin-right: pxToRem(8);
                vertical-align: middle;
                width: pxToRem(16);
            }

            @include tabs-icons('&-star', $star-icon, 12, 14, $white, $white);
            @include tabs-icons('&-user', $user-icon, 12, 12, $white, $white);
            @include tabs-icons('&-case', $case-icon, 13, 15, $white, $white);
        }
    }
}
