.header-search {
    display: inline-block;
    height: pxToRem(32);
    padding: 0 pxToRem(15) 0 pxToRem(15);
    vertical-align: middle;

    @include rwd-from($breakpoint-desktop-min) {
        border-left: 1px solid $white;
        height: pxToRem(38);
        padding: pxToRem(4) pxToRem(24) 0 pxToRem(24);
    }

    &__link {
        background: url('../files/search-icon.svg') no-repeat;
        display: inline-block;
        height: pxToRem(21);
        text-decoration: none;
        width: pxToRem(21);

        @include rwd-from($breakpoint-desktop-min) {
            height: pxToRem(30);
            width: pxToRem(30);
        }

        &:hover {
            text-decoration: underline;
        }
    }
}
