.breadcrumb {
    display: inline-block;

    &__list {
        @extend %list-reset;
        line-height: pxToRem(19);
    }

    &__item {
        color: $base-font-colour;
        display: inline-block;
        font-size: pxToRem(16);
        font-weight: $font-weight-regular;
        padding: 0 pxToRem(15) 0 0;
        vertical-align: middle;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(20);
        }

        &::before {
            color: $base-color-lead;
            content: '/';
            display: inline-block;
            padding-right: pxToRem(5);
        }

        &:first-child {
            background: url('../files/home.svg') no-repeat;
            background-size: 100%;
            height: pxToRem(27);
            margin-right: pxToRem(15);
            padding: 0;
            width: pxToRem(27);

            a {
                display: block;
                height: 100%;
                width: 100%;
            }

            &::before {
                display: none;
            }
        }

        &:last-child {
            background: none;
        }
    }

    &__link {
        color: $base-font-colour;
        font-weight: $font-weight-medium;
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }
}
