.box {
    $self: '.box';
    padding: pxToRem(17) 0;

    &__title {
        color: $base-font-colour;
        font-size: pxToRem(20);
        margin-top: 0;

        @include rwd-from($search-results-breakpoint-2) {
            font-size: pxToRem(25);
        }
    }

    &__list {
        @extend %list-reset;
        display: flex;
        flex-wrap: wrap;

        @include rwd-from($search-results-breakpoint-2) {
            flex-wrap: nowrap;
        }

        &--no-bg #{ $self }__item {
            background-color: transparent;
            border: 1px solid $light-gray;
        }
    }

    &__item {
        background-color: $white;
        border-radius: 10px;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: pxToRem(21);
        overflow: hidden;
        padding: pxToRem(40) pxToRem(40) pxToRem(30);
        position: relative;
        transform: translateZ(0);

        @include rwd-from($search-results-breakpoint-2) {
            flex: 1;
            margin: 0 pxToRem(16) 0 0;

            &:last-child {
                margin-right: 0;
            }
        }

        &--icon {
            padding: pxToRem(95) pxToRem(40) pxToRem(30);

            &::before {
                background-position: left;
                background-repeat: no-repeat;
                content: '';
                display: inline-block;
                position: absolute;
                top: pxToRem(40);
                vertical-align: sub;
                width: pxToRem(50);
            }

            @include tabs-icons('&-home', $icon-home,35,35,$base-color-lead,$base-color-lead);
            @include tabs-icons('&-courses', $icon-courses,35,49,$base-color-lead,$base-color-lead);
            @include tabs-icons('&-contact', $icon-contact,35,40,$base-color-lead,$base-color-lead);

            #{ $self }__link {
                margin: pxToRem(-95) pxToRem(-40) pxToRem(-30);
                padding: pxToRem(95) pxToRem(40) pxToRem(30);
            }
        }

        &-title {
            color: $base-font-colour;
            font-size: pxToRem(18);
            margin-top: 0;
        }
    }

    &__link {
        color: $base-font-colour;
        display: block;
        margin: pxToRem(-40) pxToRem(-40) pxToRem(-30);
        padding: pxToRem(40) pxToRem(40) pxToRem(30);
        text-decoration: none;


        @include rwd-from($breakpoint-desktop-min) {

            &:hover {
                text-decoration: none;

                #{ $self }__item-title {
                    text-decoration: underline;
                }

                #{ $self }__bg img {
                    backface-visibility: hidden;
                    transform: scale(1.1);
                }
            }

            &:focus {
                outline-offset: -4px;
            }
        }
    }

    &__desc {
        font-size: pxToRem(15);
        font-weight: $font-weight-light;
    }

    &__bg {
        background-size: cover;
        height: pxToRem(260);
        margin: pxToRem(-40) pxToRem(-40) pxToRem(35) pxToRem(-40);
        overflow: hidden;
        position: relative;
        width: calc( 100% + #{pxToRem(80)});

        &::before {
            background-color: rgba(0,0,0,0.3);
            bottom: 0;
            content: '';
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            z-index: 2;
        }

        img {
            height: pxToRem(260);
            object-fit: cover;
            transition: all 0.3s ease;
            width: 100%;
        }
    }
}
