.module-search {
    $self: '.module-search';
    margin: pxToRem(45) 0;

    @include rwd-from($breakpoint-desktop-min) {
        margin: pxToRem(68) 0 pxToRem(64) 0;
    }

    .home & {
        padding-top: pxToRem(50);
        position: relative;
        z-index: 1;

        @include rwd-from($breakpoint-desktop-min) {
            margin: 0;
            padding-top: pxToRem(160);
        }

        h2 {
            color: $white;
            font-size: pxToRem(25);
            font-weight: $font-weight-medium;
            margin-top: 0;
            text-align: center;

            @include rwd-from($breakpoint-desktop-min) {
                font-size: pxToRem(50);
            }
        }
    }

    &__group {
        position: relative;
        width: 100%;

        .home & {
            margin: 0 auto;
            max-width: pxToRem(1088);
        }
    }

    &__query {
        border: 0;
        border-radius: 10px;
        font-size: pxToRem(14);
        font-weight: $font-weight-bold;
        outline: none;
        padding: pxToRem(18) pxToRem(43) pxToRem(17) pxToRem(18);
        width: 100%;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(22);
            font-weight: $font-weight-regular;
            padding: pxToRem(27) pxToRem(80) pxToRem(20) pxToRem(44);

            &:focus {
                border-color: $base-color-lead;
            }
        }
    }

    &__btn {
        background: url('../files/search-icon-grey.svg') no-repeat;
        border: none;
        cursor: pointer;
        height: pxToRem(21);
        position: absolute;
        right: pxToRem(18);
        top: pxToRem(16);
        width: pxToRem(21);

        @include rwd-from($breakpoint-desktop-min) {
            height: pxToRem(30);
            right: pxToRem(30);
            top: pxToRem(23);
            width: pxToRem(30);
        }
    }

    .tt-category {
        color: $module-search-header-color;
        font-size: pxToRem(16);
        margin: pxToRem(20) 0 pxToRem(10) 0;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(20);
            margin: pxToRem(30) 0 pxToRem(17) 0;
        }
    }

    &.js-module-search .tt-menu {
        display: none;
    }

    .tt-menu {
        background-color: $white;
        border-radius: 0 0 10px 10px;
        list-style: none;
        margin-top: pxToRem(12);
        overflow: hidden;
        padding: 0  pxToRem(18);
        position: relative !important;
        width: 100%;
        z-index: 0 !important;

        @include rwd-from($breakpoint-desktop-min) {
            padding: 0 pxToRem(30) 0 pxToRem(44);
        }

        &::before {
            background: transparent linear-gradient(90deg, $white 40%, transparent 100%) no-repeat;
            background-color: transparent;
            background-image: -webkit-linear-gradient(0deg, $white 40%, rgba(255, 255, 255, 0.001) 100%);
            bottom: 0;
            content: '';
            display: block;
            height: 100%;
            left: pxToRem(-7);
            position: absolute;
            width: 30px;
            z-index: 2;

            @include rwd-from($breakpoint-desktop-min) {
                display: none;
            }
        }

        &::after {
            background: transparent linear-gradient(90deg, transparent 10%, $white 100%) no-repeat;
            background-color: transparent;
            background-image: -webkit-linear-gradient(0deg, rgba(255, 255, 255, 0.001) 10%, $white 100%);
            bottom: 0;
            content: '';
            display: block;
            height: 100%;
            position: absolute;
            right: 0;
            width: 60px;
            z-index: 2;

            @include rwd-from($breakpoint-desktop-min) {
                display: none;
            }
        }

        @at-root .fb-container--edu#{&} {
            z-index: 6 !important;
        }
    }

    .tt-suggestion {
        color: $module-search-text-color;
        font-size: pxToRem(16);
        text-decoration: none;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(20);
            padding: pxToRem(2) 0;

            &:hover {
                cursor: pointer;
                text-decoration: underline;
            }
        }
    }

    &__list {

        &-links {
            @extend %list-reset;
            font-weight: $font-weight-bold;
        }

        &-item:first-child &-link {
            padding-top: 0;
        }
    }

    &__list-box {
        @extend %list-reset;
        margin-top: pxToRem(18);
        max-width: 100%;
        overflow-x: auto;
        padding: 0 pxToRem(15) 0 pxToRem(30);
        white-space: nowrap;

        @include rwd-from($breakpoint-desktop-min) {
            margin: pxToRem(24) 0 pxToRem(20) 0;
            padding: 0;
            white-space: normal;
        }
    }

    .tt-dataset-primary #{ $self }__list-item {
        display: inline-block;
        margin-right: pxToRem(15);
        overflow: hidden;

        @include rwd-from($breakpoint-desktop-min) {
            margin: 0 pxToRem(20) pxToRem(20) 0;
        }
    }

    .tt-dataset-primary #{ $self }__list-item--more {

        @include rwd-from($breakpoint-desktop-min) {
            margin: 0 0 pxToRem(20) 0;
        }
    }

    .tt-dataset-primary  #{ $self }__list-link {
        background-size: cover;
        color: $white;
        display: block;
        height: pxToRem(155);
        padding: 0;
        position: relative;
        width: pxToRem(180);

        @include rwd-from($breakpoint-desktop-min) {
            height: pxToRem(200);
            width: pxToRem(256);
        }

        &::before {
            background-color: $module-search-mask-bg;
            bottom: 0;
            content: '';
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
        }
    }

    .tt-dataset-primary  #{ $self }__list-item--more #{ $self }__list-link {
        background-color: $base-color-lead;
        color: $white;
        display: none;

        @include rwd-from($breakpoint-desktop-min) {
            display: block;
            height: pxToRem(200);
            width: pxToRem(165);
        }
    }

    .tt-dataset-primary  #{ $self }__list-item--more #{ $self }__list-link::before {
        display: none;
    }

    .tt-dataset-primary #{ $self }__list-text {
        background: transparent linear-gradient(0deg, $black 0%, transparent 100%) no-repeat;
        background-color: transparent;
        background-image: -webkit-linear-gradient(90deg, $black 0%, rgba(0, 0, 0, 0.001) 100%);
        bottom: 0;
        font-size: pxToRem(14);
        font-weight: $font-weight-bold;
        padding: pxToRem(23);
        position: absolute;
        white-space: normal;
        width: 100%;
        word-break: break-word;
        z-index: 1;

        @include rwd-from($breakpoint-desktop-min) {
            bottom: 0;
            font-size: pxToRem(20);
            padding: pxToRem(33);
            @include sq-transition(padding-bottom 0.45s ease);
        }
    }

    .tt-dataset-primary #{ $self }__list-item--more #{ $self }__list-text {
        background: none;
        text-align: center;
    }

    .tt-dataset-primary #{ $self }__list-link:hover #{ $self }__list-text,
    .tt-dataset-primary #{ $self }__list-link:focus #{ $self }__list-text {

        @include rwd-from($breakpoint-desktop-min) {
            outline: none;
            padding-bottom: pxToRem(70);
            padding-top: pxToRem(70);
        }
    }

    .tt-dataset-primary #{ $self }__list-more {
        display: none;
        font-size: pxToRem(14);
        font-weight: $font-weight-regular;
        text-decoration: underline;

        @include rwd-from($breakpoint-desktop-min) {
            bottom: pxToRem(35);
            display: block;
            font-size: pxToRem(18);
            opacity: 0;
            position: absolute;
            @include sq-transition(opacity 0.45s ease);
        }
    }

    .tt-dataset-primary #{ $self }__list-link:hover #{ $self }__list-more,
    .tt-dataset-primary #{ $self }__list-link:focus #{ $self }__list-more {

        @include rwd-from($breakpoint-desktop-min) {
            opacity: 1;
        }
    }

    .tt-dataset-primary {
        margin: 0 pxToRem(-18);
        overflow: auto;
        padding: pxToRem(52) pxToRem(18) 0 pxToRem(18);
        white-space: nowrap;

        @include rwd-from($breakpoint-desktop-min) {
            margin: 0;
            padding: 0;
            white-space: normal;
        }

        .tt-category {
            bottom: pxToRem(165);
            position: absolute;

            @include rwd-from($breakpoint-desktop-min) {
                bottom: auto;
                position: relative;
            }
        }
    }

    .twitter-typeahead {
        width: 100%;
    }

    &__info {
        color: $white;
        font-size: pxToRem(14);
        position: absolute;
        top: pxToRem(55);

        @include rwd-from($breakpoint-desktop-min) {
            top: pxToRem(85);
        }

        a {
            color: $white;

            @include rwd-from($breakpoint-desktop-min) {
                &:hover {
                    text-decoration: none;
                }
            }
        }
    }

    &--bg {
        background-size: cover;
        margin: 0;
        padding-bottom: pxToRem(47);
        padding-top: pxToRem(62);
        position: relative;

        @include rwd-from($breakpoint-desktop-min) {
            padding-bottom: pxToRem(120);
            padding-top: pxToRem(75);
        }

        &::before {
            background-color: rgba(0, 0, 0, 0.5);
            bottom: 0;
            content: '';
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
        }

        &.overlay {

            &::after {
                background-color: rgba(0,0,0,0.6);
                bottom: 0;
                content: '';
                left: 0;
                position: fixed;
                right: 0;
                top: 0;
                z-index: 4;
            }
        }

        #{ $self }__group {
            margin: 0 auto;
            max-width: pxToRem(1089);
            z-index: 5;
        }

        .tt-menu {
            border-radius: 10px;
            left: 0 !important;
            margin-top: 1px;
            max-height: 60vh;
            overflow-y: auto;
            padding: 0;
            position: absolute !important;
            right: 0;
            width: 100%;
            z-index: 5 !important;

            @include rwd-from($breakpoint-desktop-min) {
                left: 0 !important;
                max-height: none;
                right: 0;
                width: 100%;
            }

            &.tt-open {
                display: flex !important;
                flex-wrap: wrap;
            }
        }

        .tt-category {
            color: $base-font-colour;
            font-size: pxToRem(15);
            font-weight: $font-weight-regular;
            margin: pxToRem(20) 0 pxToRem(10) 0;

            @include rwd-from($breakpoint-desktop-min) {
                font-size: pxToRem(20);
                margin: pxToRem(27) 0 pxToRem(10) 0;
            }
        }

        .tt-suggestion {
            font-size: pxToRem(15);
            font-weight: $font-weight-light;
            padding: pxToRem(4) 0;

            &:first-child {
                padding-top: pxToRem(27);

                @include rwd-from($breakpoint-desktop-min) {
                    padding-top: pxToRem(35);
                }
            }

            @include rwd-from($breakpoint-desktop-min) {
                padding: pxToRem(8) 0;
            }
        }

        .tt-dataset {
            padding: 0 pxToRem(20) pxToRem(5) pxToRem(20);
            width: 100%;

            &:last-child {
                padding-bottom: pxToRem(25);
            }

            @include rwd-from($breakpoint-desktop-min) {
                border-right: 1px solid $module-search-border-light;
                padding: 0 pxToRem(50) pxToRem(30) pxToRem(50);

                &:last-child {
                    border: 0;
                }
            }

            &-organic {
                flex: 0 0 100%;

                @include rwd-from($breakpoint-desktop-min) {
                    border-bottom: 1px solid $module-search-border-light;
                    border-right-width: 0;
                }

                .tt-suggestion {
                    font-size: pxToRem(15);

                    @include rwd-from($breakpoint-desktop-min) {
                        font-size: pxToRem(20);
                    }
                }
            }

            &-courses {
                margin: 0;
                overflow: hidden;
                white-space: normal;

                @include rwd-from($breakpoint-desktop-min) {
                    display: flex;
                    flex: 0 0 50%;
                    flex-wrap: wrap;
                    max-width: 50%;
                    width: 50%;
                }

                .tt-category {
                    bottom: auto;
                    position: relative;

                    @include rwd-from($breakpoint-desktop-min) {
                        flex: 0 0 100%;
                    }
                }

                .tt-suggestion {

                    @include rwd-from($breakpoint-desktop-min) {
                        flex: 0 0 100%;
                    }
                }

                small {
                    font-weight: $font-weight-light;
                }
            }

            &-people {

                @include rwd-from($breakpoint-desktop-min) {
                    display: flex;
                    flex: 0 0 50%;
                    flex-wrap: wrap;
                    max-width: 50%;
                    width: 50%;
                }

                .tt-category {

                    @include rwd-from($breakpoint-desktop-min) {
                        flex: 0 0 100%;
                    }
                }

                .tt-suggestion {

                    @include rwd-from($breakpoint-desktop-min) {
                        flex: 0 0 100%;
                        padding: pxToRem(8) 0 pxToRem(20) 0;

                        &:hover {
                            text-decoration: none;

                            h6,
                            h6 + span {
                                text-decoration: underline;
                            }
                        }
                    }

                    > span {
                        color: $module-search-light-color;
                        font-size: pxToRem(15);
                        font-weight: $font-weight-bold;
                    }
                }
            }
        }

        h6 {
            margin: 0;

            @include rwd-from($breakpoint-desktop-min) {
                font-size: pxToRem(18);
            }

            small {
                @include rwd-from($breakpoint-desktop-min) {
                    font-size: pxToRem(12);
                }
            }
        }

        .details {
            margin-top: pxToRem(8);
        }

        .details__item {
            display: block;
            padding: pxToRem(5) 0;

            &--icon {

                &::before {
                    background-position: center;
                    background-repeat: no-repeat;
                    content: '';
                    display: inline-block;
                    margin-right: pxToRem(5);
                    vertical-align: text-top;
                    width: pxToRem(17);
                }

                @include tabs-icons('&-university', $icon-university,15,15,#1F1F1F,#1F1F1F);
                @include tabs-icons('&-phone', $icon-phone,15,15,#1F1F1F,#1F1F1F);
                @include tabs-icons('&-envelope', $icon-envelope,16,14,#1F1F1F,#1F1F1F);
            }
        }

        #{ $self }__more {
            color: $base-font-colour;
            display: inline-block;
            font-size: pxToRem(15);
            font-weight: $font-weight-medium;
            padding-top: pxToRem(10);
            text-decoration: none;

            @include rwd-from($breakpoint-desktop-min) {
                align-self: flex-end;
                padding-top: 0;

                &:hover {
                    text-decoration: underline;
                }
            }
        }
        #{ $self }__query__btn {
            background-image: url('data:image/svg+xml;charset=utf-8, '+svg-fill(
                $icon-search,
                $light-gray
            ));
        }

        &.overlay #{ $self }__btn {
            background-image: url('data:image/svg+xml;charset=utf-8, '+svg-fill(
                $icon-search,
                $base-color-lead
            ));
        }
    }
}
