.module-compare {
    bottom: 0;
    display: none;
    left: 0;
    max-height: 100%;
    position: fixed;
    width: 100%;
    z-index: 10;

    .btn__compare {
        color: $white;
        font-size: pxToRem(17);
        margin-left: auto;

        &::after {
            background-image: url('data:image/svg+xml;charset=utf-8, ' +svg-fill(
                $compare__arrow, $white
            ));
        }
    }

    &__bar {
        background-color: $black;
        color: $white;
        display: flex;
        font-size: pxToRem(16);
        position: relative;
        width: 100%;

        .highlight {
            color: $white;

            @include rwd-from($breakpoint-desktop-min) {
                color: $base-color-lead;

                @at-root .fb-container--edu#{&} {
                    color: $base-color-higher-education;
                }
            }
        }

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(20);
        }

        &.content-wrapper {
            padding-bottom: pxToRem(23);
            padding-top: pxToRem(43);

            @include rwd-from($breakpoint-desktop-min) {
                padding-bottom: pxToRem(30);
                padding-top: pxToRem(30);
            }
        }
    }

    &__data {
        display: inline-block;
        position: relative;
        top: pxToRem(-9);

        @include rwd-from($breakpoint-desktop-min) {
            top: 0;
        }
    }

    &__number {
        color: $base-color-lead;
        font-size: pxToRem(20);
        padding-right: pxToRem(10);

        @include rwd-from($breakpoint-desktop-min) {
            padding-right: pxToRem(5);
        }
    }

    &__clear {
        bottom: pxToRem(-19);
        color: $base-color-lead;
        font-size: pxToRem(14);
        left: 0;
        position: absolute;

        @at-root .fb-container--edu#{&} {
            color: $base-color-higher-education;
        }

        @include rwd-from($breakpoint-desktop-min) {
            bottom: auto;
            font-size: pxToRem(18);
            left: auto;
            padding-left: pxToRem(50);
            position: relative;
            vertical-align: top;

            &:hover {
                text-decoration: none;
            }
        }
    }

    &__close {
        align-self: center;
        color: inherit;
        font-size: pxToRem(17);
        padding-right: pxToRem(30);
        position: relative;
        text-decoration: none;
        text-transform: uppercase;

        &::before {
            background-color: $base-color-lead;
            bottom: 0;
            content: '';
            height: 2px;
            margin: auto 0;
            position: absolute;
            right: 0;
            top: 0;
            transform: rotate(45deg);
            transition: right 0.4s ease;
            width: pxToRem(18);

            @at-root .fb-container--edu#{&} {
                background-color: $base-color-higher-education;
            }
        }

        &::after {
            background-color: $base-color-lead;
            bottom: 0;
            content: '';
            height: 2px;
            margin: auto 0;
            position: absolute;
            right: 0;
            top: 0;
            transform: rotate(-45deg);
            transition: right 0.4s ease;
            width: pxToRem(18);

            @at-root .fb-container--edu#{&} {
                background-color: $base-color-higher-education;
            }
        }
    }

    &__wrapper {
        background-color: $body-bg;
        max-height: calc(100vh - #{pxToRem(90)});
        overflow-y: auto;
        position: relative;

        @include rwd-from($breakpoint-desktop-min) {
            background-color: $white;
            max-height: calc(100vh - #{pxToRem(88)});
        }

        &.content-wrapper {
            padding: 0;

            @include rwd-from($breakpoint-desktop-min) {
                padding: 0 pxToRem($content-wrapper-padding);
            }
        }
    }

    &__title {
        color: $base-font-colour;
        font-size: pxToRem(18);
        margin: 0;
        padding-top: pxToRem(35);
    }

    &__phrase {
        background-color: $search-results-phrase-bg;
        border-radius: pxToRem(19);
        clear: left;
        color: $white;
        display: block;
        float: left;
        font-size: pxToRem(14);
        margin: pxToRem(-66) 0 0 pxToRem(29);
        padding: pxToRem(10) pxToRem(18) pxToRem(11) pxToRem(18);
        position: relative;
    }

    &__bg {
        background-color: #e1e1e1;
        background-size: cover;
        border-radius: pxToRem(10) pxToRem(10) 0 0;
        height: pxToRem(198);
        margin: pxToRem(29) 0 0 0;
        overflow: hidden;
        width: 100%;

        @include rwd-from($breakpoint-desktop-min) {
            height: pxToRem(260);
            margin: pxToRem(46) 0 0 0;
        }

        img {
            height: pxToRem(198);
            object-fit: cover;
            width: 100%;

            @include rwd-from($breakpoint-desktop-min) {
                height: pxToRem(260);
            }
        }
    }

    &__list {
        background-color: $white;
        box-shadow: 0 3px 50px rgba(0, 0, 0, 0.16);
        margin-top: pxToRem(37);
        padding: 0 0 pxToRem(50) pxToRem(20);
        table-layout: fixed;
        width: 100%;

        @include rwd-from($breakpoint-desktop-min) {
            box-shadow: none;
            margin-top: 0;
            padding: 0 0 pxToRem(50) 0;
        }

        tr {
            &:first-of-type {
                width: 100%;

                td,
                th {
                    border: 0;
                }
            }
        }

        th {
            border-top: 1px solid rgba(185, 185, 185, 0.5);
            display: none;
            padding: pxToRem(40) pxToRem(18);
            text-align: left;

            @include rwd-from($breakpoint-desktop-min) {
                display: table-cell;
                width: pxToRem(317);
            }
        }

        td {
            border-top: 1px solid rgba(185, 185, 185, 0.5);
            padding: pxToRem(40) pxToRem(18);
            position: relative;
            vertical-align: top;
            width: 86vw;

            &:last-child {
                padding-right: pxToRem(37);
            }

            @include rwd-from($module-compare-breakpoint-1) {
                border-left: 1px solid rgba(185, 185, 185, 0.5);
                width: 45vw;
            }

            @include rwd-from($breakpoint-desktop-min) {
                width: pxToRem(450);
            }

            &.datath {
                position: relative;

                &::before {
                    content: attr(data-th) ': \00a0';
                    display: inline-block;
                    font-weight: bold;
                    padding: 0 pxToRem(17);
                    width: 49%;

                    @include rwd-from($breakpoint-desktop-min) {
                        display: none;
                    }
                }

                span {
                    display: inline-block;
                    padding: 0 pxToRem(17);
                    text-align: center;
                    vertical-align: top;
                    width: 49%;

                    @include rwd-from($breakpoint-desktop-min) {
                        padding: 0;
                        width: 100%;
                    }

                    &::before {
                        background: rgba(185, 185, 185, 0.5);
                        bottom: 0;
                        content: '';
                        left: 50%;
                        position: absolute;
                        top: 0;
                        width: 1px;

                        @include rwd-from($module-compare-breakpoint-1) {
                            display: none;
                        }
                    }
                }
            }
        }
    }

    &__desc {
        font-size: pxToRem(15);
        font-weight: $font-weight-regular;
        line-height: pxToRem(24);
    }

    &__remove {
        border: 1px solid $base-color-lead;
        border-radius: pxToRem(19);
        color: $base-color-lead;
        display: inline-block;
        font-size: pxToRem(14);
        font-weight: $font-weight-medium;
        margin-top: 0;
        padding: pxToRem(8) pxToRem(43) pxToRem(9) pxToRem(22);
        position: relative;
        text-decoration: none;

        @at-root .fb-container--edu#{&} {
            border-color: $base-color-higher-education;
            color: $base-color-higher-education;
        }

        @include rwd-from($breakpoint-desktop-min) {
            margin-top: pxToRem(20);
        }

        &::before {
            background-color: $base-color-lead;
            bottom: 0;
            content: '';
            height: 1px;
            margin: auto 0;
            position: absolute;
            right: pxToRem(19);
            top: 0;
            transform: rotate(45deg);
            transition: right 0.4s ease;
            width: pxToRem(13);

            @at-root .fb-container--edu#{&} {
                background-color: $base-color-higher-education;
            }
        }

        &::after {
            background-color: $base-color-lead;
            bottom: 0;
            content: '';
            height: 1px;
            margin: auto 0;
            position: absolute;
            right: pxToRem(19);
            top: 0;
            transform: rotate(-45deg);
            transition: right 0.4s ease;
            width: pxToRem(13);

            @at-root .fb-container--edu#{&} {
                background-color: $base-color-higher-education;
            }
        }

        @include rwd-from($breakpoint-desktop-min) {

            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__link {
        bottom: pxToRem(60);
        color: $black;
        display: inline-block;
        font-size: pxToRem(16);
        font-weight: $font-weight-bold;
        padding-bottom: 8px;
        position: absolute;
        text-decoration: none;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(20);
        }
    }

    &__overview {
        align-self: flex-end;
        background-color: $module-compare-overview-bg;
        font-size: pxToRem(16);
        margin-top: pxToRem(20);
        padding: pxToRem(40) pxToRem(40) pxToRem(40) pxToRem(68);
        position: relative;
        width: 100%;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(19);
        }

        &::before {
            background-color: $base-color-lead;
            bottom: pxToRem(40);
            content: '';
            left: pxToRem(40);
            position: absolute;
            top: pxToRem(40);
            width: 2px;

            @at-root .fb-container--edu#{&} {
                background-color: $base-color-higher-education;
            }
        }

        &-title {
            color: $base-color-lead;
            font-size: pxToRem(20);
            margin: 0 0 pxToRem(20) 0;

            @at-root .fb-container--edu#{&} {
                color: $base-color-higher-education;
            }
        }

        &-list {
            margin-bottom: 0;
        }
    }

    &__dt {
        font-weight: $font-weight-bold;
    }

    &__dd {
        margin: 0 0 pxToRem(20) 0;

        &:last-child {
            margin-bottom: 0;
        }
    }

    ul {

        li {
            font-size: pxToRem(18);
            font-weight: $font-weight-regular;
            list-style-image: url('data:0');
            list-style-type: none;
            padding: pxToRem(6) 0;
            position: relative;

            &::before {
                background-color: $base-color-lead;
                border-radius: 50%;
                bottom: 0;
                content: '';
                height: pxToRem(10);
                left: pxToRem(-27);
                margin: auto 0;
                position: absolute;
                top: 0;
                width: pxToRem(10);

                @at-root .fb-container--edu#{&} {
                    background-color: $base-color-higher-education;
                }
            }
        }
    }
}
