.fb-header {
    background-color: $header-bg;
    overflow: hidden;
    position: relative;
    z-index: 1;

    .home & {
        background-color: transparent;
        width: 100%;
    }

    &-bottom {

        .home & {
            position: relative;

            &::after {
                background-color: rgba(255,255,255,0.25);
                bottom: 0;
                content: '';
                height: 1px;
                left: 0;
                margin: 0 auto;
                position: absolute;
                right: 0;
                width: calc( 100% - #{pxToRem($content-wrapper-padding-mobile * 2)} );

                @include rwd-from($breakpoint-desktop-min) {
                    width: calc( 100% - #{pxToRem($content-wrapper-padding * 2)} );
                }
            }
        }
    }

    &-left {
        display: inline-block;
        margin: pxToRem(60) 0 pxToRem(39) 0;
        vertical-align: middle;

        @include rwd-from($breakpoint-desktop-min) {
            margin: pxToRem(59) 0 pxToRem(49) 0;
        }
    }

    &-right {
        display: inline-block;
        float: right;
        margin: pxToRem(57) 0 pxToRem(35) 0;
        text-align: right;
        vertical-align: middle;

        @include rwd-from($breakpoint-desktop-min) {
            margin: pxToRem(55) 0 pxToRem(45) 0;
            width: calc(100% - #{pxToRem(260)});
        }
    }
}
