.module-filter {
    $self: '.module-filter';

    &__wrapper {
        display: block;
        position: relative;

        &::before {
            background-color: rgba(185, 185, 185, 0.5);
            content: '';
            height: 1px;
            left: 0;
            position: absolute;
            right: 0;
            top: pxToRem(56);
        }

        @include rwd-from($breakpoint-desktop-min) {
            border-bottom: 1px solid rgba(185, 185, 185, 0.5);
            display: flex;

            &::before {
                display: none;
            }
        }

        &-mobile {
            display: none;

            @include rwd-from($breakpoint-desktop-min) {
                display: flex;
            }
        }
    }

    &--dark #{ $self }__wrapper {

        @include rwd-from($breakpoint-desktop-min) {
            background-color: $black;
            padding: 0 pxToRem(54);
        }
    }

    &__title {
        color: $gray;
        display: table;
        font-size: pxToRem(20);
        margin: 0;
        padding: pxToRem(15) pxToRem(36) pxToRem(15) 0;
        position: relative;

        @include rwd-from($breakpoint-desktop-min) {
            align-self: center;
            padding: 0;
        }

        &::before {
            background-color: $base-color-lead;
            bottom: 0;
            content: '';
            height: pxToRem(3);
            margin: auto 0;
            position: absolute;
            right: pxToRem(10);
            top: 0;
            transform: rotate(45deg);
            transition: right 0.4s ease;
            width: pxToRem(17);

            @at-root .fb-container--edu#{&} {
                background-color: $base-color-higher-education;
            }

            @include rwd-from($breakpoint-desktop-min) {
                display: none;
            }
        }

        &::after {
            background-color: $base-color-lead;
            bottom: 0;
            content: '';
            height: pxToRem(3);
            margin: auto 0;
            position: absolute;
            right: 0;
            top: 0;
            transform: rotate(-45deg);
            transition: right 0.4s ease;
            width: pxToRem(17);

            @at-root .fb-container--edu#{&} {
                background-color: $base-color-higher-education;
            }

            @include rwd-from($breakpoint-desktop-min) {
                display: none;
            }
        }

        &.active {

            &::before {
                right: pxToRem(5);

                @include rwd-from($breakpoint-desktop-min) {
                    display: none;
                }
            }

            &::after {
                right: pxToRem(5);

                @include rwd-from($breakpoint-desktop-min) {
                    display: none;
                }
            }
        }
    }

    &--dark #{ $self }__title {

        @include rwd-from($breakpoint-desktop-min) {
            color: $white;
            margin-left: auto;

            &:first-of-type {
                margin-left: 0;
            }
        }
    }

    &__list {
        @extend %list-reset;
        color: $gray;
        padding: 0 0 pxToRem(45) 0;

        @include rwd-from($breakpoint-desktop-min) {
            padding: 0 0 0 pxToRem(30);
        }
    }

    &--dark #{ $self }__list {

        @include rwd-from($breakpoint-desktop-min) {
            color: $white;
            padding: 0 0 0 pxToRem(10);
        }
    }

    &__item {
        border-bottom: 1px solid rgba(185, 185, 185, 0.25);
        cursor: pointer;

        @include rwd-from($breakpoint-desktop-min) {
            border-width: 0;
            display: inline-block;
            font-size: pxToRem(20);
            vertical-align: middle;

            &:first-child {
                border-left: 1px solid rgba(185, 185, 185, 0.5);
            }
        }

        &-title {
            display: block;
            font-size: pxToRem(16);
            font-weight: $font-weight-medium;
            padding: pxToRem(17) pxToRem(30) pxToRem(17) 0;
            position: relative;

            @include rwd-from($breakpoint-desktop-min) {
                border-right: 1px solid rgba(185, 185, 185, 0.5);
                font-size: pxToRem(17);
                padding: pxToRem(15) pxToRem(60) pxToRem(15) pxToRem(26);
                text-transform: uppercase;
            }

            &::after {
                background-image: none;
                background-color: $base-color-lead;
                -webkit-mask-image: url('../files/arrow-white.svg');
                -webkit-mask-repeat: no-repeat;
                -webkit-mask-size: 100%;
                mask-image: url('../files/arrow-white.svg');
                mask-repeat: no-repeat;
                mask-size: 100%;

                background-size: 100%;
                bottom: 0;
                content: '';
                height: pxToRem(13);
                margin: auto 0;
                position: absolute;
                right: 0;
                top: 0;
                transition: transform 0.4s ease;
                width: pxToRem(22);

                @include rwd-from($breakpoint-desktop-min) {
                    height: pxToRem(11);
                    right: pxToRem(24);
                    width: pxToRem(19);
                }
            }

            &.active {
                &::after {
                    transform: rotate(180deg);
                }
            }

            &:focus {
                outline: none;
            }
        }

        &:focus {
            outline: none;

            @include rwd-from($breakpoint-desktop-min) {
                outline: auto;
            }
        }

        @include rwd-from($breakpoint-desktop-min) {

            &:focus-within {

                #{ $self }__item-title {
                    &::after {
                        transform: rotate(180deg);
                    }
                }

                #{ $self }__facets {
                    display: block;
                }
            }

            &:focus,
            &:hover {

                #{ $self }__item-title {
                    &::after {
                        transform: rotate(180deg);
                    }
                }

                #{ $self }__facets {
                    display: block;
                }
            }
        }
    }

    &--dark #{ $self }__item {
        @include rwd-from($breakpoint-desktop-min) {
            border: 0;
        }
    }

    &--dark #{ $self }__item-title {

        @include rwd-from($breakpoint-desktop-min) {
            border: 0;
            font-size: pxToRem(20);
            padding: pxToRem(30) pxToRem(40) pxToRem(30) pxToRem(20);
            text-transform: none;

            &::after {
                right: pxToRem(8);
            }
        }
    }

    &__facets {
        display: none;
        margin: 0;
        width: 100%;
        z-index: 3;

        @include rwd-from($breakpoint-desktop-min) {
            left: 0;
            padding-top: pxToRem(17);
            position: absolute;
            top: 100%;
        }

        .content-wrapper {
            padding: 0;

            @include rwd-from($breakpoint-desktop-min) {
                background-color: $module-filter-facets-bg;
                box-shadow: 0 31px 99px rgba(0, 0, 0, 0.19);
                padding: pxToRem(80);
            }
        }

        &-list {
            @extend %list-reset;
            overflow: hidden;

            @include rwd-from($breakpoint-desktop-min) {
                display: inline-block;
                padding-right: pxToRem(32);
                vertical-align: top;
                width: 33%;

                &:nth-child(n+3) {
                    padding-right: 0;
                }
            }

            + #{ $self }__facets-list {

                #{ $self }__facets-item {
                    &:first-child {
                        #{ $self }__facets-link {
                            border-top: 1px solid rgba(185, 185, 185, 0.5);

                            @include rwd-from($breakpoint-desktop-min) {
                                border-top-width: 0;
                            }
                        }
                    }
                }
            }
        }

        &-item {

            &:last-child {

                #{ $self }__facets-link {
                    border-bottom-width: 0;

                    @include rwd-from($breakpoint-desktop-min) {
                        border-bottom-width: 1px;
                    }
                }
            }
        }

        &-link {
            border-bottom: 1px solid rgba(185, 185, 185, 0.5);
            color: $base-font-colour;
            display: block;
            padding: pxToRem(20) 0 pxToRem(20) pxToRem(36);
            position: relative;
            text-decoration: none;

            &::after {
                border: 1px solid $gray;
                border-radius: 50%;
                bottom: 0;
                content: '';
                height: 15px;
                left: 0;
                margin: auto 0;
                position: absolute;
                top: 0;
                width: 15px;
            }

            @include rwd-from($breakpoint-desktop-min) {
                padding: pxToRem(20) 0 pxToRem(20) 30px;

                &:hover {
                    text-decoration: underline;
                }
            }

            &.active {

                &::before {
                    background: url('~src/files/check-mark.svg') no-repeat;
                    background-size: 100%;
                    bottom: 0;
                    content: '';
                    height: 5px;
                    left: 4px;
                    margin: auto 0;
                    position: absolute;
                    top: 0;
                    width: 7px;
                }

                &::after {
                    border-color: $base-color-lead;
                }
            }

            span {
                background-color: rgba(228, 43, 18, 0.05);
                color: $base-color-lead;
                float: right;
                font-size: pxToRem(12);
                padding: pxToRem(4) pxToRem(8);

                @at-root .fb-container--edu#{&} {
                    background-color: rgba(185, 185, 185, 0.15);
                    color: $base-color-higher-education;
                }
            }
        }
    }

    &--dark #{ $self }__facets-link {

        &::after {
            border-radius: 0;
        }
    }

    &--dark #{ $self }__facets {

        @include rwd-from($breakpoint-desktop-min) {
            padding-top: 0;
        }
    }

    &__checkbox-list {
        @extend %list-reset;
        padding-bottom: pxToRem(30);

        @include rwd-from($breakpoint-desktop-min) {
            align-self: center;
            justify-content: flex-end;
            margin-left: auto;
            padding-bottom: 0;
        }
    }

    &__checkbox-item {
        padding: pxToRem(10) 0 pxToRem(10) 0;

        @include rwd-from($breakpoint-desktop-min) {
            display: inline-block;
            padding: 0 0 0 pxToRem(35);
        }
    }

    &__label {
        color: $gray;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(17);
        }
    }

    .btn__compare {
        position: absolute;
        right: 0;
        top: pxToRem(15);

        @include rwd-from($breakpoint-desktop-min) {
            align-self: center;
            margin-left: auto;
            position: relative;
            right: auto;
            top: auto;
        }
    }

    input[type='radio'] + label {
        font-size: pxToRem(17);
        margin: pxToRem(10) 0 0 pxToRem(5);
        text-transform: uppercase;

        @include rwd-from($breakpoint-desktop-min) {
            margin: 0 0 0 pxToRem(30);
        }

        &::before {
            margin-right: pxToRem(15);
        }
    }

    &__radio-list {
        @extend %list-reset;

        @include rwd-from($breakpoint-desktop-min) {
            align-self: center;
            display: flex;
            margin-right: pxToRem(40);
        }
    }

    &__radio-item {
        display: inline-block;
        padding-right: pxToRem(15);

        @include rwd-from($breakpoint-desktop-min) {
            padding-right: 0;
        }
    }

    &--dark {

        input[type='radio'] + label {
            text-transform: none;
        }

        @include rwd-from($breakpoint-desktop-min) {

            input[type='radio'] + label {
                color: $white;
                font-size: pxToRem(20);

                &::before {
                    border-color: $white;
                }
            }

            input[type='radio']:checked + label::before {
                background-color: $white;
            }

            .btn__compare {
                color: $white;

                &::after {
                    background-image: url('data:image/svg+xml;charset=utf-8, ' +svg-fill(
                        $compare__arrow, $white
                    ));

                    @at-root .fb-container--edu#{&} {
                        background-image: url('data:image/svg+xml;charset=utf-8, '+svg-fill(
                            $compare__arrow,
                            $light-gray
                        ));
                    }
                }
            }
        }
    }
}

.content-wrapper--col {

    .module-filter {
        $self: '.module-filter';

        &--dark {

            @include rwd-from($breakpoint-desktop-min) {

                #{$self}__item {

                    &:first-child {

                        #{$self}__item-title {
                            margin-top: 0;
                        }
                    }
                }

                #{$self}__item-title {
                    background: $black;
                    margin-top: pxToRem(20);

                    &::after {
                        background: url('../files/arrow-white.svg') no-repeat;
                        background-size: 100%;
                    }
                }

                #{$self}__facets .content-wrapper {
                    background-color: $white;
                }
            }

            &.module-filter--no-results #{$self}__title {
                opacity: 0.5;

                @include rwd-from($breakpoint-desktop-min) {
                    opacity: 1;
                }
            }

            &.module-filter--no-results #{$self}__item {

                @include rwd-from($breakpoint-desktop-min) {

                    &:hover {

                        #{$self}__item-title::after {
                            transform: none;
                        }
                    }
                }
            }

            &.module-filter--no-results #{$self}__item-title {
                color: rgba(0, 0, 0, 0.4);
                pointer-events: none;

                &::after {
                    background: url('../files/arrow-gray.svg') no-repeat;
                    background-size: 100%;
                    opacity: 0.5;
                }

                @include rwd-from($breakpoint-desktop-min) {
                    background: rgba(29, 29, 29, 0.1);
                }
            }
        }

        &__wrapper {

            @include rwd-from($breakpoint-desktop-min) {
                background: none;
                border: 0;
                flex-wrap: wrap;
                padding: 0;
            }
        }

        &__list {

            @include rwd-from($breakpoint-desktop-min) {
                padding: 0;
            }
        }

        &__item {

            @include rwd-from($breakpoint-desktop-min) {
                cursor: default;
                width: 100%;

                &:first-child {
                    border: 0;
                }

                &:focus-within {

                    #{ $self }__facets {
                        display: none;
                    }
                }

                &:focus,
                &:hover {

                    #{ $self }__facets {
                        display: none;
                    }
                }
            }
        }

        &__item-title {

            @include rwd-from($breakpoint-desktop-min) {
                border-right: 0;
                cursor: pointer;
                font-size: pxToRem(18);
                padding: pxToRem(23) pxToRem(60) pxToRem(23) pxToRem(30);

                &::after {
                    right: pxToRem(30);
                }

                &:focus-within {

                    #{ $self }__facets {
                        display: none;
                    }
                }

                &:focus {
                    outline: 1px solid $white;
                    outline-offset: -3px;
                }

                &:focus,
                &:hover {

                    #{ $self }__facets {
                        display: none;
                    }
                }

                &.open {
                    &::after {
                        transform: rotate(180deg);
                    }
                }
            }
        }

        &__title {

            @include rwd-from($breakpoint-desktop-min) {
                display: none;
            }
        }

        &__facets {

            @include rwd-from($breakpoint-desktop-min) {
                display: none;
                left: auto;
                min-height: 0;
                position: relative;
                top: auto;

                .content-wrapper {
                    background-color: transparent;
                    box-shadow: none;
                    padding: pxToRem(10) pxToRem(30);
                }
            }

            &-link {

                &.active::after {

                    @at-root .fb-container--edu#{&} {
                        border-color: $base-color-higher-education;
                    }
                }

                &.active::before {

                    @at-root .fb-container--edu#{&} {
                        background: url('~src/files/check-mark-blue.svg') no-repeat;
                        background-size: 100%;
                    }
                }

                @include rwd-from($breakpoint-desktop-min) {
                    font-size: pxToRem(18);

                    &:focus {
                        outline-offset: 4px;
                    }
                }
            }

            &-list {

                @include rwd-from($breakpoint-desktop-min) {
                    display: block;
                    overflow: visible;
                    padding-right: 0;
                    width: 100%;
                }
            }
        }

        &__separate {

            @include rwd-from($breakpoint-desktop-min) {
                border-top: 1px solid $light-gray;
                margin-top: pxToRem(20);
                padding: pxToRem(20) 0;
                width: 100%;
            }
        }

        .btn__compare {

            @include rwd-from($breakpoint-desktop-min) {
                color: $base-font-colour;
                margin: pxToRem(30) 0 0 pxToRem(32);
            }
        }
    }
}
