.module-curator {
    $self: '.module-curator';

    &__list {
        @extend %list-reset;
        display: flex;
        flex-wrap: wrap;

        @include rwd-from($search-results-breakpoint-2) {
            flex-wrap: nowrap;
        }
    }

    &--no-bg #{ $self }__item {
        background-color: transparent;
        border: 1px solid $light-gray;
    }

    &__item {
        background-color: $white;
        border-radius: 10px;
        display: flex;
        flex-flow: column wrap;
        margin: 0 0 pxToRem(30) 0;
        overflow: hidden;
        padding: pxToRem(30) 0;
        width: 100%;

        @include rwd-from($search-results-breakpoint-2) {
            flex: 1;
            margin-right: pxToRem(15);
            width: auto;
        }

        @include rwd-from($breakpoint-desktop-min) {
            margin-bottom: pxToRem(20);
        }

        &:last-child {
            margin-right: 0;
        }
    }

    &__link {
        color: $base-font-colour;
        text-decoration: none;

        @include rwd-from($breakpoint-desktop-min) {
            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__bg {
        border-radius: 10px;
        flex: 0 0 pxToRem(60);
        height: pxToRem(60);
        margin: 0 0 0 pxToRem(29);
        overflow: hidden;
        width: pxToRem(60);

        img {
            height: pxToRem(60);
            object-fit: cover;
            width: pxToRem(60);
        }
    }

    &__bg + #{ $self }__title {
        margin: 0 pxToRem(29) 0 pxToRem(15);
    }

    &__top {
        display: flex;
    }

    &__item--simple #{ $self }__top {
        flex-wrap: wrap;
    }

    &__item--simple cite {
        font-style: normal;
        font-weight: $font-weight-bold;
        margin: 0 pxToRem(29) 0 pxToRem(29);
    }

    &__title {
        color: $base-font-colour;
        font-size: pxToRem(15);
        margin: 0 pxToRem(29) pxToEm(5) pxToRem(29);
    }

    &__item--simple  #{ $self }__title {
        font-size: pxToRem(20);
        width: 100%;
    }

    &__content {
        border-top: 1px solid $module-curator-border-separate;
        margin: pxToRem(20) pxToRem(29) 0 pxToRem(29);
        padding: pxToRem(20) 0 0 0;
    }

    &__item--simple  #{ $self }__content {
        border-top-width: 0;
    }

    &__desc {
        font-size: pxToRem(15);
        font-weight: $font-weight-light;
        margin: 0;

        &::before {
            background-color: #434343;
            border-radius: 4px;
            color: $white;
            content: 'Ad';
            display: inline;
            font-size: pxToRem(10);
            font-weight: $font-weight-bold;
            line-height: pxToRem(24);
            padding: pxToRem(3) pxToRem(5);
        }
    }

    &__item--simple #{ $self }__desc::before {
        display: none;
    }
    .btn--link {
        margin-left: pxToRem(29);
        margin-top: auto;
        padding-top: pxToRem(25);
        text-decoration: none;

        @include rwd-from($breakpoint-desktop-min) {
            &:hover {
                text-decoration: underline;
            }
        }
    }
}

.search-results__tools {

    + .module-curator {
        margin-top: pxToRem(25);

        @include rwd-from($breakpoint-desktop-min) {
            margin-top: pxToRem(36);
        }
    }
}

.search-results .module-curator .btn--link {
    margin-top: auto;

    @include rwd-from($breakpoint-desktop-min) {

        &:focus {
            outline: none;
            text-decoration: underline;
        }
    }
}
