// Globals Colours
// Base Colors
$black: #000;
$white: #fff;
$gray: #434343;
$light-gray: #b9b9b9;
$red: #da1007;
$blue: #194caf;

$body-bg: #f0f0f0;

$base-color-lead: $red;
$base-color-higher-education: $blue;

// Base font
$base-font-scale: 1;  // The font scale multiplier (e.g. 2 = 32px/2em/2rem)
$base-font-pixel: 16; // Used for font size calculations & conversions
$scale-font-size: 0.8;
$base-font-colour: $black;
$base-font-family: 'Gilroy', 'Arial', sans-serif;

// Selection highlighting
$content-selection-highlight: #b3d4fc;

// modules
$content-wrapper-padding: 140;
$content-wrapper-padding-mobile: 35;
$border-radius-global: 10px;

// Media queries breakpoints
$breakpoint-mobile-min: 360;
$breakpoint-tablet-min: 768;
$breakpoint-desktop-min: 1025;
$breakpoint-text-max: 1640 + ( 2 * $content-wrapper-padding );
$breakpoint-desktop-max: 1920;

// Font weights
$font-weight-light: 200;
$font-weight-regular: 300;
$font-weight-medium: 500;
$font-weight-bold: 700;

