.pagination {
    $self: '.pagination';
    margin: pxToRem($base-module-spacing-mobile) 0 0 0;
    text-align: center;

    @include rwd-from($breakpoint-desktop-min) {
        margin: pxToRem($base-module-spacing/2) 0 0 0;
    }

    &__nav {
        display: flex;
        justify-content: center;
        padding: 0;

        @include rwd-from($breakpoint-desktop-min) {
            padding: 0 pxToRem(15);
        }
    }

    &__pages-list {
        @include sq-list-reset();
        display: flex;

        @include rwd-from($breakpoint-desktop-min) {
            display: flex;
            flex-wrap: wrap;
        }
    }

    &__label {
        line-height: pxToRem(15);

        @include rwd-from($breakpoint-desktop-min) {
            line-height: pxToRem(36);
        }
    }

    &__item {
        background-color: $white;
        color: $gray;
        display: inline-block;
        flex-shrink: 0;
        font-size: pxToRem(15);
        height: pxToRem(40);
        margin: 0 pxToRem(1) pxToRem(1);
        transition: 0.2s background-color ease-in, 0.2s box-shadow ease-in;
        width: pxToRem(40);

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(20);
            height: pxToRem(66);
            margin: 0 pxToRem(2);
            width: pxToRem(66);
        }

        &--active,
        &:hover {
            background-color: $pagination-theme-color;
            .module-pagination__label {
                color: $white;
            }
        }

        &--more {
            &:hover {
                background-color: transparent;
                box-shadow: none;
                color: $pagination-theme-color;
            }
        }
    }

    &__item-navigation {
        width: auto;
    }

    &__item-navigation #{ $self }__link {
        position: relative;

        &::before {
            background-image: url('~src/files/arrow-gray.svg');
            background-position: center;
            background-repeat: no-repeat;
            background-size: 100%;
            bottom: 0;
            content: '';
            height: pxToRem(12);
            margin: auto 0;
            position: absolute;
            top: 0;
            width: pxToRem(12);

            @include rwd-from($breakpoint-desktop-min) {
                height: pxToRem(19);
                width: pxToRem(19);
            }
        }

        &:hover {

            &::before {
                background-image: url('~src/files/arrow-white.svg');
            }
        }
    }

    &__item-previous {
        margin-left: 0;
    }

    &__item-previous #{ $self }__link {
        padding: 0 pxToRem(16) 0 pxToRem(30);

        @include rwd-from($breakpoint-desktop-min) {
            padding: 0 pxToRem(40) 0 pxToRem(64);
        }

        &::before {
            left: pxToRem(12);
            transform: rotate(90deg);

            @include rwd-from($breakpoint-desktop-min) {
                left: pxToRem(35);
            }
        }
    }

    &__item-next {
        margin-right: 0;
    }

    &__item-next #{ $self }__link {
        padding: 0 pxToRem(30) 0 pxToRem(16);

        @include rwd-from($breakpoint-desktop-min) {
            padding: 0 pxToRem(64) 0 pxToRem(40);
        }

        &::before {
            right: pxToRem(12);
            transform: rotate(-90deg);

            @include rwd-from($breakpoint-desktop-min) {
                right: pxToRem(35);
            }
        }
    }

    &__link {
        align-items: center;
        color: $pagination-theme-color;
        display: flex;
        height: 100%;
        justify-content: center;
        text-align: center;
        text-decoration: none;
        width: 100%;

        &:hover {
            color: $white;
        }
    }

    &__current {
        align-items: center;
        color: $white;
        display: flex;
        height: 100%;
        justify-content: center;
        width: 100%;
    }

    &__more {
        align-items: center;
        display: flex;
        height: 100%;
        justify-content: center;
        width: 100%;
    }
}
