.search-results {
    $self: '.search-results';
    background-color: $search-results-bg;
    padding: pxToRem(37) 0 pxToRem(40) 0;

    @at-root .fb-container--edu#{&} {
        padding: pxToRem(27) 0 pxToRem(40) 0;
    }

    @include rwd-from($breakpoint-desktop-min) {
        padding: pxToRem(130) 0 pxToRem(100) 0;
    }

    .content-wrapper + .content-wrapper {
        margin-top: pxToRem(90);
    }

    &__btn {
        background-color: $black;
        border: 1px solid $black;
        color: $white;
        display: inline-block;
        font-size: pxToRem(14);
        margin: 0 0 0 pxToRem(20);
        padding: pxToRem(6.5) pxToRem(15) pxToRem(5.5) pxToRem(15);
        text-decoration: none;
        transition: background-color 0.3s ease, color 0.3s ease;
        vertical-align: middle;

        @include rwd-from($breakpoint-desktop-min) {

            &:hover {
                background-color: $body-bg;
                color: $black;
            }
        }
    }

    &__header {

        &-title {
            color: $black;
            font-size: pxToRem(36);
            line-height: pxToRem(32);
            margin: pxToRem(-25) 0 pxToRem(30) 0;
            text-align: left;

            @include rwd-from($breakpoint-desktop-min) {
                font-size: pxToRem(50);
                margin: pxToRem(22) 0 pxToRem(30) 0;
                text-align: center;
            }
        }

        &-desc {
            font-size: pxToRem(15);
            font-weight: $font-weight-regular;
            line-height: pxToRem(32);
            text-align: left;

            @include rwd-from($breakpoint-desktop-min) {
                font-size: pxToRem(18);
                text-align: center;
            }
        }
    }

    &--no-bg #{ $self }__item {
        background-color: transparent;
        border: 1px solid $light-gray;

        &--twitter {
            border: 0;

            article {
                background-color: transparent;
                border: 1px solid $light-gray;
            }
        }
    }

    &__total {
        display: block;
        font-size: pxToRem(15);
        padding: pxToRem(5) 0;

        @at-root .fb-container--edu#{&} {
            margin-bottom: pxToRem(20);
        }

        @include rwd-from($breakpoint-desktop-min) {
            display: inline-block;
            font-size: pxToRem(20);
            padding-left: pxToRem(20);
        }
    }

    &__section-title {
        color: $base-font-colour;
        display: inline-block;
        font-size: pxToRem(20);
        font-weight: $font-weight-medium;
        margin: 0;
        text-transform: uppercase;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(25);
        }
    }

    &__list {
        @extend %list-reset;
        display: flex;
        flex-wrap: wrap;
        margin-top: pxToRem(50);
        width: 100%;

        @include rwd-from($search-results-breakpoint-1) {
            margin: pxToRem(50) pxToRem(-8) 0 pxToRem(-8);
            width: calc( 100% + #{pxToRem(16)});

            @at-root .fb-container--edu#{&} {
                margin-top: pxToRem(16);
            }
        }

        &--list-view {
            display: block;
            margin: pxToRem(50) 0 0;

            @include rwd-from($search-results-breakpoint-1) {
                width: 100%;
            }
        }
    }

    &__item {
        background-color: $white;
        border-radius: 10px;
        flex: 0 0 100%;
        margin: pxToRem(15) 0;
        overflow: hidden;
        position: relative;

        &:first-child {
            margin-top: 0;
        }

        @include rwd-from($search-results-breakpoint-1) {
            flex: 0 0 calc(50% - #{pxToRem(16.1)});
            margin: pxToRem(8);
            max-width: calc(50% - #{pxToRem(16.1)});
            width: calc(50% - #{pxToRem(16.1)});

            &:nth-child(-n+2) {
                margin-top: 0;
            }
        }

        @include rwd-from($search-results-breakpoint-2) {
            flex: 0 0 calc(33.333% - #{pxToRem(16.1)});
            max-width: calc(33.333% - #{pxToRem(16.1)});
            width: calc(33.333% - #{pxToRem(16.1)});

            &:nth-child(-n+3) {
                margin-top: 0;
            }
        }

        @include rwd-from($breakpoint-desktop-min) {
            flex: 0 0 calc(25% - #{pxToRem(16.1)});
            max-width: calc(25% - #{pxToRem(16.1)});
            width: calc(25% - #{pxToRem(16.1)});

            &:nth-child(-n+4) {
                margin-top: 0;
            }
        }

        &--col {
            background-color: transparent;
            display: flex;
            flex: 0 0 calc( 100% - #{pxToRem(61)});
            max-width: calc( 100% - #{pxToRem(61)});
            width: calc( 100% - #{pxToRem(61)});

            article {
                background-color: $white;
                border-radius: 10px;
                overflow: hidden;

                @include rwd-from($search-results-breakpoint-2) {
                    flex: 1;
                    margin-right: pxToRem(15);
                    width: auto;
                }

                &:nth-child(3n) {
                    margin-right: 0;
                }
            }
        }

        &--video {
            > a {
                display: block;
                position: relative;

                &::before {
                    background-image: url('data:image/svg+xml;charset=utf-8, '+svg-fill(
                        $play-icon,
                        $white
                    ));
                    background-repeat: no-repeat;
                    background-size: 100%;
                    bottom: 0;
                    content: '';
                    height: pxToRem(47);
                    left: 0;
                    margin: auto;
                    position: absolute;
                    right: 0;
                    top: 0;
                    width: pxToRem(47);
                    z-index: 2;
                }

                &::after {
                    background: rgba(0,0,0,0.4);
                    content: '';
                    height: 100%;
                    left: 0;
                    position: absolute;
                    top: 0;
                    width: 100%;
                    z-index: 1;
                }

                figure {
                    overflow: hidden;
                }

                img {
                    transition: transform 0.3s ease;
                }

                @include rwd-from($breakpoint-desktop-min) {
                    &:hover {

                        img {
                            transform: scale(1.2);
                        }
                    }
                }
            }
        }

        .btn--link {
            text-decoration: none;

            @include rwd-from($breakpoint-desktop-min) {

                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }

    &__list--list-view #{ $self }__item {
        margin: 0 0 pxToRem(30) 0;
        max-width: 100%;
        width: 100%;

        @include rwd-from($breakpoint-desktop-min) {
            display: flex;
            margin: 0 0 pxToRem(20) 0;
        }
    }

    &__bg {
        background-color: $light-gray;
        background-size: cover;
        height: pxToRem(260);
        margin: 0;
        width: 100%;

        img {
            height: pxToRem(260);
            object-fit: cover;
            width: 100%;
        }
    }

    &__item--twitter #{ $self }__bg,
    &__list--list-view #{ $self }__item--twitter #{ $self }__bg {
        border-radius: 10px;
        display: inline-block;
        height: pxToRem(80);
        margin: pxToRem(20) 0 0 pxToRem(20);
        overflow: hidden;
        width: pxToRem(80);
    }

    &__item--twitter img,
    &__list--list-view #{ $self }__item--twitter img {
        height: pxToRem(80);
        width: pxToRem(80);
    }

    &__list--list-view #{ $self }__bg {
        display: none;
    }

    &__list--list-view #{ $self }__item--default #{ $self }__bg,
    &__list--list-view #{ $self }__item--video #{ $self }__bg,
    &__list--list-view #{ $self }__item--event #{ $self }__bg,
    &__list--list-view #{ $self }__item--people #{ $self }__bg {
        display: none;

        @include rwd-from($breakpoint-desktop-min) {
            display: inline-block;
            height: auto;
            width: pxToRem(260);
        }
    }

    &__list--list-view #{ $self }__item--default img,
    &__list--list-view #{ $self }__item--video img,
    &__list--list-view #{ $self }__item--event img,
    &__list--list-view #{ $self }__item--people img {
        height: pxToRem(200);
        width: 100%;

        @include rwd-from($breakpoint-desktop-min) {
            height: 100%;
            width: pxToRem(260);
        }
    }

    &__list--list-view #{ $self }__item--video > a {
        height: pxToRem(200);
        width: 100%;

        @include rwd-from($breakpoint-desktop-min) {
            height: auto;
            width: pxToRem(260);
        }
    }

    &__list--list-view #{ $self }__item--video #{ $self }__bg {
        height: 100%;
        width: 100%;
    }

    &__time {
        left: pxToRem(29);
        position: absolute;
        text-align: center;
        top: pxToRem(306);

        @include rwd-from($search-results-breakpoint-1) {
            top: pxToRem(298);
        }
    }

    &__list--list-view #{ $self }__time {
        display: none;
    }

    &__list--list-view #{ $self }__item--event #{ $self }__time {
        display: block;
        padding: pxToRem(20) 0 0 0;
        position: relative;
        right: auto;
        text-align: left;
        top: auto;


        @include rwd-from($breakpoint-desktop-min) {
            backdrop-filter: blur(10px);
            background-color: rgba(255,255,255,0.7);
            border-radius: 10px;
            bottom: pxToRem(10);
            left: pxToRem(10);
            padding: pxToRem(10) pxToRem(20);
            position: absolute;
            text-align: center;
        }
    }

    &__month {
        color: $base-color-lead;
        display: block;
        font-size: pxToRem(12);
        font-weight: $font-weight-bold;

        @at-root .fb-container--edu#{&} {
            color: $base-color-higher-education;
        }
    }

    &__list--list-view #{ $self }__item--event #{ $self}__month {
        display: inline-block;

        @include rwd-from($breakpoint-desktop-min) {
            display: block;
        }
    }

    &__day {
        display: block;
        font-size: pxToRem(29);
        font-weight: $font-weight-medium;
    }

    &__list--list-view #{ $self }__item--event #{ $self}__day {
        display: inline-block;
        font-size: pxToRem(12);

        @include rwd-from($breakpoint-desktop-min) {
            display: block;
            font-size: pxToRem(29);
        }
    }

    &__time + #{ $self }__content {
        padding: pxToRem(40) pxToRem(35) pxToRem(33) pxToRem(78);

        @include rwd-from($search-results-breakpoint-1) {
            padding: pxToRem(32) pxToRem(31) pxToRem(34) pxToRem(80);
        }
    }

    &__list--list-view #{ $self }__time + #{ $self }__content {
        padding: pxToRem(26) pxToRem(25);

        @include rwd-from($breakpoint-desktop-min) {
            padding: pxToRem(26) pxToRem(45);
        }
    }

    &__list--list-view #{ $self }__item--event #{ $self }__time + #{ $self }__content {
        padding: pxToRem(10) pxToRem(25) pxToRem(26) pxToRem(25);

        @include rwd-from($breakpoint-desktop-min) {
            padding: pxToRem(26) pxToRem(45);
        }
    }

    &__content {
        display: flex;
        flex-wrap: wrap;
        height: calc( 100% - #{pxToRem(260)} );
        padding: pxToRem(30);

        @include rwd-from($breakpoint-desktop-min) {
            padding: pxToRem(33) pxToRem(31) pxToRem(34) pxToRem(35);
        }

        input[type='checkbox'] + label {

            &::before {
                background-size: 8px;
                border-radius: 50%;
                height: 14px;
                width: 14px;
            }
        }
    }

    &__list--list-view #{ $self }__content {
        display: block;
        height: 100%;
        overflow: hidden;
        position: relative;
        width: 100%;

        &::after {
            background-color: rgba(185, 185, 185, 0.3);
            content: '';
            height: calc( 100% - #{pxToRem(52)});
            left: 50%;
            position: absolute;
            top: pxToRem(26);
            width: 1px;
        }

        @include rwd-from($breakpoint-desktop-min) {
            display: flex;
            padding: pxToRem(26) pxToRem(45);

            &::after {
                display: none;
            }
        }
    }

    &__item--twitter #{ $self }__content,
    &__list--list-view #{ $self }___item--twitter #{ $self }__content {
        height: auto;
        padding: pxToRem(15) pxToRem(30) pxToRem(35) pxToRem(30);

        @include rwd-from($breakpoint-desktop-min) {
            padding: pxToRem(15) pxToRem(25) pxToRem(35) pxToRem(25);
        }

        #{ $self }__desc {
            margin: 0;
        }
    }

    &__list--list-view #{ $self }__item--default #{ $self }__content,
    &__list--list-view #{ $self }__item--video #{ $self }__content,
    &__list--list-view #{ $self }__item--event #{ $self }__content,
    &__list--list-view #{ $self }__item--people #{ $self }__content,
    &__list--list-view #{ $self }__item--course #{ $self }__content {
        display: block;
        height: auto;

        &::after {
            display: none;
        }

        #{ $self }__title,
        #{ $self }__desc {
            max-width: 100%;
            width: 100%;
        }
    }

    &__list--list-view #{ $self }__item--twitter {

        #{ $self }__title,
        #{ $self }__desc {
            flex: 0 0 100%;
            max-width: 100%;
            width: 100%;
        }
    }

    &__list--list-view .contact__item {

        @include rwd-from($breakpoint-desktop-min) {
            display: inline-block;
        }
    }

    &__title {
        color: $gray;
        font-size: pxToRem(18);
        margin: 0;
    }

    &__item--twitter #{ $self }__header {
        display: inline-block;
        margin: pxToEm(20) 0 0 pxToRem(20);
        vertical-align: top;
        width: calc(100% - #{pxToEm(140)});

        time {
            font-size: pxToEm(15);
            font-weight: $font-weight-light;
        }
    }

    &__sub-title {
        font-size: pxToRem(15);
    }

    &__list--list-view #{ $self }__title {
        clear: left;
        display: inline-block;
        float: left;
        font-size: pxToRem(16);
        margin-bottom: pxToRem(45);
        padding-right: pxToRem(10);
        width: 50%;

        @include rwd-from($breakpoint-desktop-min) {
            align-self: center;
            flex: 0 0 25%;
            font-size: pxToRem(20);
            margin-bottom: 0;
            max-width: 25%;
            padding-right: pxToRem(20);
            width: 25%;
        }
    }

    &__item--twitter #{ $self }__title {
        margin: pxToEm(10) 0 pxToEm(5) 0;
    }

    &__list--list-view #{ $self }__item--default #{ $self }__title,
    &__list--list-view #{ $self }__item--video #{ $self }__title,
    &__list--list-view #{ $self }__item--event #{ $self }__title,
    &__list--list-view #{ $self }__item--people #{ $self }__title,
    &__list--list-view #{ $self }__item--course #{ $self }__title {
        margin-bottom: 0;
    }

    &__link {
        color: $gray;
        font-size: pxToRem(18);
        text-decoration: none;
        background: none;
        border: none;
        box-shadow: none;
        padding: 0;
        font-weight: bold;
        text-align: left;
    
        @include rwd-from($breakpoint-desktop-min) {

            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__list--list-view #{ $self }__link {
        font-size: pxToRem(16);

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(20);
        }
    }

    &__desc {
        font-size: pxToRem(15);
        font-weight: $font-weight-regular;
        line-height: pxToRem(24);

        @include rwd-from($breakpoint-desktop-min) {
            display: block;
        }
    }

    &__list--list-view #{ $self }__desc {
        clear: left;
        display: none;
        float: left;
        font-size: pxToRem(13);
        line-height: pxToRem(20);
        padding-right: pxToRem(20);
        width: 50%;

        @at-root .fb-container--edu#{&} {
            display: block;
            padding-right: 0;
        }

        @include rwd-from($breakpoint-desktop-min) {
            align-self: center;
            display: inline-block;
            flex: 0 0 35%;
            font-size: pxToRem(15);
            line-height: pxToRem(24);
            max-width: 35%;
            width: 35%;
        }
    }

    &__info {
        border-top: 1px solid #e2e2e2;
        display: block;
        font-size: pxToRem(13);
        font-weight: $font-weight-medium;
        padding-bottom: pxToRem(12);
        padding-top: pxToRem(14);
        width: 100%;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(15);
            font-weight: $font-weight-bold;
        }
    }

    &__list--list-view #{ $self }__info {
        align-self: center;
        border-bottom: 1px solid rgba(185, 185, 185, 0.3);
        border-top-width: 0;
        display: block;
        flex: 0 0 100%;
        margin: 0;
        max-width: 100%;
        padding: pxToRem(10) 0 pxToRem(10) 0;
        width: 100%;

        &:last-of-type {
            border-bottom-width: 0;
        }

        @include rwd-from($breakpoint-desktop-min) {
            border-bottom-width: 0;
            border-left: 1px solid rgba(67, 67, 67, 0.2);
            display: table-cell;
            padding: pxToRem(20) pxToRem(30);
            vertical-align: middle;
        }

        input[type='checkbox'] + label {
            font-size: pxToRem(13);

            @include rwd-from($breakpoint-desktop-min) {
                font-size: pxToRem(14);
            }
        }
    }

    &__list--list-view #{ $self }__item--course #{ $self }__info {

        @include rwd-from($breakpoint-desktop-min) {
            padding: pxToRem(5) pxToRem(30);
        }
    }

    &__phrase {
        background-color: $search-results-phrase-bg;
        border-radius: pxToRem(19);
        bottom: calc( 100% - #{pxToRem(235)});
        color: $white;
        font-size: pxToRem(14);
        left: pxToRem(29);
        max-width: 30%;
        padding: pxToRem(10) pxToRem(18) pxToRem(11) pxToRem(18);
        position: absolute;
    }

    &__list--list-view #{ $self }__phrase {
        display: none;
    }

    &__compare {
        bottom: calc( 100% - #{pxToRem(235)});
        position: absolute;
        right: pxToRem(31);

        input[type='checkbox'] + label {
            background-color: $white;
            border-radius: pxToRem(19);
            display: block;
            font-size: pxToRem(14);
            margin-left: 0;
            padding: pxToRem(10) pxToRem(18) pxToRem(11) pxToRem(18);
            transition: color,background-color 0.3s ease-in-out;

            &::before {
                display: none;
            }

            &::after {
                background-image: url('data:image/svg+xml;charset=utf-8, '+svg-fill(
                    $compare__arrow,
                    $gray
                ));
                background-repeat: no-repeat;
                content: '';
                display: inline-block;
                height: pxToRem(13);
                margin-left: pxToRem(13);
                vertical-align: middle;
                width: pxToRem(21);
            }
        }

        input[type='checkbox']:checked + label {
            background-color: $base-color-lead;
            color: $white;

            &::after {
                background-image: url('data:image/svg+xml;charset=utf-8, ' +svg-fill(
                    $compare__arrow,
                    $white
                ));
            }
        }
    }

    &__list--list-view #{ $self }__compare {
        bottom: pxToRem(20);
        display: inline-block;
        left: pxToRem(25);
        padding: pxToRem(10) 0 pxToRem(10) 0;
        position: absolute;
        right: auto;

        input[type='checkbox']:checked + label {
            background-color: $base-color-lead;
            border-color: $base-color-lead;
            color: $white;

            &::after {
                background-image: url('data:image/svg+xml;charset=utf-8, ' +svg-fill(
                    $compare__arrow,
                    $white
                ));
            }
        }

        @include rwd-from($breakpoint-desktop-min) {
            border-left: 1px solid rgba(67, 67, 67, 0.2);
            bottom: auto;
            display: table-cell;
            left: auto;
            padding: pxToRem(20) 0 pxToRem(20) pxToRem(30);
            position: relative;
            top: auto;
            vertical-align: middle;
            width: pxToRem(180);

            input[type='checkbox'] + label {
                display: inline-block;
            }
        }

        input[type='checkbox'] + label {
            background-color: $white;
            border: 1px solid $base-color-lead;
            color: $base-color-lead;
            font-size: pxToRem(13);
            padding: pxToRem(6) pxToRem(13) pxToRem(6) pxToRem(13);

            @include rwd-from($breakpoint-desktop-min) {
                border-color: $gray;
                color: inherit;
                font-size: pxToRem(14);
                padding: pxToRem(10) pxToRem(18) pxToRem(11) pxToRem(18);
            }

            &::after {
                background-image: url('data:image/svg+xml;charset=utf-8, ' +svg-fill(
                    $compare__arrow, $base-color-lead
                ));
                height: pxToRem(10);
                margin-left: pxToRem(6);
                width: pxToRem(16);

                @include rwd-from($breakpoint-desktop-min) {
                    background-image: url('data:image/svg+xml;charset=utf-8, ' +svg-fill(
                        $compare__arrow, $gray
                    ));
                    height: pxToRem(13);
                    margin-left: pxToRem(13);
                    width: pxToRem(21);
                }
            }
        }
    }

    &__list--list-view #{ $self }__item--course #{ $self }__compare {

        @include rwd-from($breakpoint-desktop-min) {
            padding: 0 0 0 pxToRem(30);
            width: pxToRem(280);
        }
    }

    #{ $self }__table #{ $self }__compare {
        bottom: auto;
        left: auto;
        margin: pxToRem(10) 0;
        position: relative;
        right: auto;
        top: auto;
        white-space: nowrap;

        @include rwd-from($breakpoint-desktop-min) {
            float: right;
            margin: 0;
        }
    }

    #{ $self }__table #{ $self }__compare input[type='checkbox'] + label {
        background-color: transparent;
        border: 1px solid $black;
        display: inline-block;
    }

    #{ $self }__table #{ $self }__compare input[type='checkbox']:checked + label {
        background-color: $base-color-lead;
        border-color: $base-color-lead;
    }

    &__bottom {
        align-self: flex-end;
        padding-top: pxToRem(20);
        width: 100%;
    }

    &__item--default #{ $self }__bottom,
    &__item--video #{ $self }__bottom,
    &__item--event #{ $self }__bottom,
    &__item--people #{ $self }__bottom,
    &__item--course #{ $self }__bottom {
        padding-top: 0;
    }

    &__list--list-view #{ $self }__bottom {
        display: inline-block;
        padding: 0 0 0 pxToRem(20);
        width: 50%;

        @include rwd-from($breakpoint-desktop-min) {
            display: table;
            max-width: 40%;
            padding: 0;
            table-layout: fixed;
            width: 40%;
        }
    }

    &__list--list-view #{ $self }__item--default #{ $self }__bottom,
    &__list--list-view #{ $self }__item--video #{ $self }__bottom,
    &__list--list-view #{ $self }__item--event #{ $self }__bottom,
    &__list--list-view #{ $self }__item--people #{ $self }__bottom,
    &__list--list-view #{ $self }__item--course #{ $self }__bottom {
        max-width: 100%;
        padding: 0;
        width: 100%;
    }

    &__tools {
        margin-bottom: pxToRem(35);
        overflow: hidden;

        @include rwd-from($breakpoint-desktop-min) {
            margin-bottom: pxToRem(90);
        }

        &-title {
            clear: left;
            color: $base-font-colour;
            display: inline-block;
            float: left;
            font-size: pxToRem(20);
            font-weight: $font-weight-regular;
            margin: 0 0 pxToRem(5) 0;
            text-transform: uppercase;

            @include rwd-from($breakpoint-desktop-min) {
                font-size: pxToRem(30);
                width: auto;
            }
        }

        &-link {
            margin-right: 0;
            vertical-align: middle;

            @include rwd-from($breakpoint-desktop-min) {
                display: inline-block;
                margin-right: pxToRem(40);

                &:hover {
                    text-decoration: none;
                }
            }
        }

        &-right {
            display: inline-block;
            float: right;
            overflow: visible;
            text-align: right;

            #{ $self }__total {
                line-height: pxToRem(20);
                margin: 0 0 pxToRem(20) 0;

                @include rwd-from($breakpoint-desktop-min) {
                    margin: 0 pxToRem(29) 0 0;
                    padding-top: 0;
                    vertical-align: middle;
                }
            }
        }
    }

    &__icon {
        display: inline-block;        
        background-color: #000;
        height: pxToRem(28);        
        margin-left: pxToRem(16);
        opacity: 0.2;
        position: relative;
        vertical-align: middle;
        width: pxToRem(28);
        @include sq-transition(opacity 0.45s ease);

        @include rwd-from($breakpoint-desktop-min) {
            height: pxToRem(34);
            margin-left: pxToRem(20);
            width: pxToRem(34);

            &:hover {
                opacity: 1;
            }
        }

        &.active {
            opacity: 1;
        }

        &--box {
            // Implementations may define the color using the background-color property
            background-image: none;            
            -webkit-mask-image: url('../files/grid1.svg');
            -webkit-mask-repeat: no-repeat;
            -webkit-mask-size: 100%;
            mask-image: url('../files/grid1.svg');
            mask-repeat: no-repeat;
            mask-size: 100%;
        }

        &--list {
            // Implementations may define the color using the background-color property
            background-image: none;
            -webkit-mask-image: url('../files/grid2.svg');
            -webkit-mask-repeat: no-repeat;
            -webkit-mask-size: 100%;
            mask-image: url('../files/grid2.svg');
            mask-repeat: no-repeat;
            mask-size: 100%;
        }
    }

    .btn--link {
        display: inline-block;
        margin-top: pxToRem(20);
    }

    &__link-all {
        color: $base-font-colour;
        display: block;
        font-size: pxToRem(20);
        line-height: pxToRem(24);
        text-decoration: none;

        @include rwd-from($breakpoint-desktop-min) {
            display: inline-block;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__table {
        display: block;
        margin-top: pxToRem(10);
        width: 100%;

        @include rwd-from($breakpoint-desktop-min) {
            display: table;
            margin-top: pxToRem(50);
        }

        &:first-of-type {
            margin-top: pxToRem(40);

            @include rwd-from($breakpoint-desktop-min) {
                margin-top: pxToRem(50);
            }
        }

        &-caption {
            background-color: rgba(0, 0, 0, 0.05);
            display: block;
            font-size: pxToRem(20);
            margin-bottom: pxToRem(8);
            padding: pxToRem(10) pxToRem(25);
            text-align: left;

            @include rwd-from($breakpoint-desktop-min) {
                display: table-caption;
                font-size: pxToRem(25);
                margin-bottom: pxToRem(16);
                padding: pxToRem(20) pxToRem(35);
            }
        }

        thead {
            display: none;

            @include rwd-from($breakpoint-desktop-min) {
                display: table-header-group;
            }
        }

        tbody {
            display: block;

            @include rwd-from($breakpoint-desktop-min) {
                display: table-row-group;
            }
        }

        &-th {
            display: none;
            font-size: pxToRem(18);
            padding: pxToRem(10) 0;
            text-align: left;

            @include rwd-from($breakpoint-desktop-min) {
                display: table-cell;
            }
        }

        &-td {
            display: block;
            font-size: pxToRem(15);
            font-weight: $font-weight-light;
            padding: pxToRem(5) 0;
            width: 100%;

            @include rwd-from($breakpoint-desktop-min) {
                display: table-cell;
                font-size: pxToRem(18);
                padding: pxToRem(10) pxToRem(15) pxToRem(10) 0;
                width: 11.25%;
            }

            &.datath {
                position: relative;

                &::before {
                    content: attr(data-th) ': \00a0';
                    display: inline-block;
                    font-weight: bold;
                    padding: 0;
                    //width: 49%;

                    @include rwd-from($breakpoint-desktop-min) {
                        display: none;
                    }
                }
            }

            &--title {

                @include rwd-from($breakpoint-desktop-min) {
                    width: 15%;
                }
            }

            &--desc {
                font-size: pxToRem(15);

                @include rwd-from($breakpoint-desktop-min) {
                    width: 40%;
                }
            }

            a {
                color: $base-font-colour;
                text-decoration: none;

                @include rwd-from($breakpoint-desktop-min) {

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }

        tr {
            display: block;
            padding: pxToRem(10) 0;
            width: 100%;

            @include rwd-from($breakpoint-desktop-min) {
                display: table-row;
                padding: 0;
            }
        }

        tbody tr {
            border-bottom: 1px solid rgba(0,0,0,0.3);

            &:last-child {
                border: 0;
            }

            @include rwd-from($breakpoint-desktop-min) {
                border: 0;
            }
        }
    }
}

.content-wrapper--col {

    .search-results {
        $self: '.search-results';

        @include rwd-from($breakpoint-desktop-min) {
            padding: 0;
        }

        &__tools {

            @include rwd-from($breakpoint-desktop-min) {
                margin-bottom: 0;
            }
        }

        &__item {

            @include rwd-from($search-results-breakpoint-1) {
                flex: 0 0 calc(50% - #{pxToRem(16.1)});
                margin: pxToRem(8);
                max-width: calc(50% - #{pxToRem(16.1)});
                width: calc(50% - #{pxToRem(16.1)});

                &:nth-child(-n+2) {
                    margin-top: 0;
                }
            }

            @include rwd-from($search-results-breakpoint-2) {
                flex: 0 0 calc(33.333% - #{pxToRem(16.1)});
                max-width: calc(33.333% - #{pxToRem(16.1)});
                width: calc(33.333% - #{pxToRem(16.1)});

                &:nth-child(-n+3) {
                    margin-top: 0;
                }
            }

            &--col {
                display: flex;
                flex: 0 0 100%;
                flex-wrap: wrap;
                max-width: 100%;
                width: 100%;

                @include rwd-from($search-results-breakpoint-1) {
                    flex: 0 0 calc(100% - #{pxToRem(16)});
                    max-width: calc(100% - #{pxToRem(16)});
                    width: calc(100% - #{pxToRem(16)});
                }

                @include rwd-from($search-results-breakpoint-2) {
                    flex-wrap: nowrap;
                }

                article {
                    margin-bottom: pxToEm(30);
                    width: 100%;

                    &:last-child {
                        margin-bottom: 0;
                    }

                    @include rwd-from($search-results-breakpoint-1) {
                        margin-bottom: pxToEm(15);
                    }

                    @include rwd-from($search-results-breakpoint-2) {
                        margin-bottom: 0;
                        width: auto;
                    }
                }
            }
        }

        &__list--list-view #{ $self }__item {
            margin: 0 0 pxToRem(30) 0;
            max-width: 100%;
            width: 100%;

            @include rwd-from($breakpoint-desktop-min) {
                margin: 0 0 pxToRem(20) 0;
            }
        }
    }
}

.content-wrapper--small {

    .search-results {
        padding: 0;
    }
}

.module-curator {

    + .search-results {
        $self: '.search-results';

        &__list {
            margin-top: 0;
        }
    }
}
