.related-links {
    $self: '.related-links';
    margin-top: pxToRem(57);

    &__title {
        color: $base-font-colour;
        font-size: pxToRem(20);
        font-weight: $font-weight-regular;
        text-align: center;
    }

    &__list {
        @extend %list-reset;
        break-inside: avoid;
        -webkit-column-break-inside: avoid;
        column-count: 1;
        margin: pxToRem(20) auto 0 auto;
        max-width: 100%;
        page-break-inside: avoid;
        position: relative;
        text-align: center;

        @include rwd-from($search-results-breakpoint-2) {
            column-count: 2;
            max-width: 80%;
            text-align: left;

            &::before {
                background-color: $light-gray;
                bottom: 0;
                content: '';
                left: 50%;
                position: absolute;
                top: 0;
                width: 1px;
            }
        }

        @include rwd-from($breakpoint-desktop-min) {
            margin: pxToRem(40) auto 0 auto;
            max-width: 60%;
        }
    }

    &__item {

        @include rwd-from($search-results-breakpoint-2) {
            padding-left: pxToRem(100);
        }
    }

    &__link {
        color: $base-font-colour;
        font-size: pxToRem(18);
        line-height: pxToRem(49);
        text-decoration: none;

        @include rwd-from($search-results-breakpoint-2) {

            &:hover {
                text-decoration: underline;
            }
        }
    }
}
