.tabs {
    $self: '.tabs';
    margin-bottom: pxToRem(30);
    margin-top: pxToRem(32);
    overflow: hidden;

    @include rwd-from($breakpoint-desktop-min) {
        margin-bottom: pxToRem(20);
        margin-top: 0;
        z-index: 2;
    }

    &__list {
        @extend %list-reset;
        border: 2px solid $base-color-lead;
        border-radius: 10px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        overflow: hidden;

        @include rwd-from($breakpoint-desktop-min) {
            align-items: flex-end;
            border: 0;
            flex-wrap: nowrap;
            overflow-y: auto;
            padding: 0;
        }

        @at-root .fb-container--edu#{&} {
            border-color: $base-color-higher-education;
        }
    }

    &--disabled #{ $self }__list {
        border-color: $light-gray;
    }

    &__item {
        width: 100%;

        @include rwd-from($breakpoint-desktop-min) {
            height: pxToRem(70);
            overflow: hidden;
            width: auto;
        }
    }

    &__link {
        background-color: transparent;
        color: $base-font-colour;
        display: block;
        font-size: pxToRem(15);
        font-weight: $font-weight-bold;
        height: auto;
        margin-top: -20%;
        padding: pxToRem(13) pxToRem(20);
        position: relative;
        text-decoration: none;
        transition: margin-top 0.3s linear;
        width: 100%;

        &::after {
            background-color: rgba(185, 185, 185, 0.5);
            bottom: 0;
            content: '';
            height: 1px;
            left: pxToRem(20);
            position: absolute;
            right: pxToRem(20);
        }

        @include rwd-from($breakpoint-desktop-min) {
            background-color: $tabs-bg;
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
            color: $white;
            font-size: pxToRem(18);
            margin: 0 pxToRem(10);
            padding: pxToRem(22) pxToRem(34) pxToRem(23) pxToRem(34);
            transition: color,background-color 0.3s ease-in-out;
            white-space: nowrap;
            width: auto;

            &:hover {
                background-color: rgba(240,240,240,0.8);
                color: $base-font-colour;
            }

            &:focus {
                outline-offset: -2px;
            }

            &::after {
                display: none;
            }
        }

        &--disabled {
            color: $light-gray;
            pointer-events: none;

            @include rwd-from($breakpoint-desktop-min) {
                color: $tabs-disabled;
            }

            &::before {
                opacity: 0.3;
            }
        }

        &.active {
            align-items: center;
            background-color: $body-bg;
            color: $base-font-colour;
            display: flex;
            margin-top: 0;
            position: relative;
            z-index: 3;

            &::after {
                display: none;
            }

            @include rwd-from($breakpoint-desktop-min) {
                background-color: $white;
                display: block;
            }
        }

        &--disabled.active {
            color: $light-gray;
            filter: grayscale(100%);

            @include rwd-from($breakpoint-desktop-min) {
                opacity: 0.7;
            }
        }

        &--icon {

            @include rwd-from($breakpoint-desktop-min) {
                padding: pxToRem(22) pxToRem(34) pxToRem(23) pxToRem(23);
            }

            &::before {
                background-position: center;
                background-repeat: no-repeat;
                content: '';
                display: inline-block;
                margin-right: pxToRem(12);
                vertical-align: sub;
                width: pxToRem(24);
            }

            @include tabs-icons('&-file', $icon-file,20,14,$base-font-colour,$base-color-lead);
            @include tabs-icons('&-graduation', $icon-graduation,16,21,$base-font-colour,$base-color-lead);
            @include tabs-icons('&-users', $icon-users,23,19,$base-font-colour,$base-color-lead);
            @include tabs-icons('&-social', $icon-social,19,18,$base-font-colour,$base-color-lead);
            @include tabs-icons('&-event', $icon-event,18,18,$base-font-colour,$base-color-lead);
            @include tabs-icons('&-globe', $icon-globe,20,20,$base-font-colour,$base-color-lead);

            @include rwd-from($breakpoint-desktop-min) {

                @include tabs-icons('&-file', $icon-file,24,18,$white,$base-color-lead);
                @include tabs-icons('&-graduation', $icon-graduation,18,24,$white,$base-color-lead);
                @include tabs-icons('&-users', $icon-users,24,20,$white,$base-color-lead);
                @include tabs-icons('&-social', $icon-social,23,22,$white,$base-color-lead);
                @include tabs-icons('&-event', $icon-event,22,22,$white,$base-color-lead);
                @include tabs-icons('&-globe', $icon-globe,25,25,$white,$base-color-lead);
            }

            @at-root .fb-container--edu#{&} {

                @include tabs-icons('&-file', $icon-file,20,14,$base-font-colour,$base-color-higher-education);
                @include tabs-icons('&-graduation', $icon-graduation,16,21,$base-font-colour,$base-color-higher-education);
                @include tabs-icons('&-users', $icon-users,23,19,$base-font-colour,$base-color-higher-education);
                @include tabs-icons('&-social', $icon-social,19,18,$base-font-colour,$base-color-higher-education);
                @include tabs-icons('&-event', $icon-event,18,18,$base-font-colour,$base-color-higher-education);
                @include tabs-icons('&-globe', $icon-globe,20,20,$base-font-colour,$base-color-higher-education);

                @include rwd-from($breakpoint-desktop-min) {

                    @include tabs-icons('&-file', $icon-file,24,18,$white,$base-color-higher-education);
                    @include tabs-icons('&-graduation', $icon-graduation,18,24,$white,$base-color-higher-education);
                    @include tabs-icons('&-users', $icon-users,24,20,$white,$base-color-higher-education);
                    @include tabs-icons('&-social', $icon-social,23,22,$white,$base-color-higher-education);
                    @include tabs-icons('&-event', $icon-event,22,22,$white,$base-color-higher-education);
                    @include tabs-icons('&-globe', $icon-globe,25,25,$white,$base-color-higher-education);
                }
            }
        }
    }

    &.open #{ $self }__link {
        margin-top: 0;
        opacity: 1;
    }

    &.open #{ $self }__link.active {
        background-color: #e9ebee;
        color: $base-color-lead;

        @at-root .fb-container--edu#{&} {
            color: $base-color-higher-education;
        }
    }

    &--disabled.open #{ $self }__link.active {
        color: $light-gray;
    }

    &__menu-btn {
        background: url('data:image/svg+xml;charset=utf-8, '+svg-fill(
            $menu-icon,
            $base-color-lead
        ));
        background-position: right pxToRem(17) center;
        background-repeat: no-repeat;
        background-size: pxToRem(23) pxToRem(16);
        border: 0;
        height: pxToRem(51);
        position: absolute;
        right: pxToRem($content-wrapper-padding-mobile);
        width: calc( 100% - #{pxToRem($content-wrapper-padding-mobile * 2)} );
        z-index: 4;

        &:focus {
            outline: none;
        }

        @at-root .fb-container--edu#{&} {
            background: url('data:image/svg+xml;charset=utf-8, '+svg-fill(
                $menu-icon,
                $base-color-higher-education
            ));
            background-position: right pxToRem(17) center;
            background-repeat: no-repeat;
            background-size: pxToRem(23) pxToRem(16);
        }
    }

    &--disabled #{ $self }__menu-btn {
        background: url('data:image/svg+xml;charset=utf-8, '+svg-fill(
            $menu-icon,
            $light-gray
        ));
        background-position: right pxToRem(17) center;
        background-repeat: no-repeat;
        background-size: pxToRem(23) pxToRem(16);
    }

    &.open #{ $self }__menu-btn {
        background: url('data:image/svg+xml;charset=utf-8, '+svg-fill(
            $close-icon,
            $base-color-lead
        ));
        background-position: right pxToRem(17) center;
        background-repeat: no-repeat;
        background-size: pxToRem(15) pxToRem(15);

        @at-root .fb-container--edu#{&} {
            background: url('data:image/svg+xml;charset=utf-8, '+svg-fill(
                $close-icon,
                $base-color-higher-education
            ));
            background-position: right pxToRem(17) center;
            background-repeat: no-repeat;
            background-size: pxToRem(15) pxToRem(15);
        }
    }

    &--disabled.open #{ $self }__menu-btn {
        background: url('data:image/svg+xml;charset=utf-8, '+svg-fill(
            $close-icon,
            $light-gray
        ));
        background-position: right pxToRem(17) center;
        background-repeat: no-repeat;
        background-size: pxToRem(15) pxToRem(15);
    }
}

.module-search--bg + .tabs {

    @include rwd-from($breakpoint-desktop-min) {
        margin-top: pxToRem(-70);
        padding: 0;
    }
}

.module-search--bg + .tabs  .tabs__list {

    @include rwd-from($breakpoint-desktop-min) {
        border-bottom: 1px solid $tabs-active-bg;
    }
}

.module-search--bg + .tabs  .tabs__link.active {

    @include rwd-from($breakpoint-desktop-min) {
        background-color: $tabs-active-bg;
    }
}
