.filter-list {
    $self: '.filter-list';
    display: inline-block;
    width: 100%;

    @include rwd-from($breakpoint-desktop-min) {
        clear: left;
        display: flex;
        float: left;
    }

    &__title {
        color: inherit;
        font-size: pxToRem(20);
        font-weight: $font-weight-medium;
        margin: 0;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(30);
            margin: pxToRem(13) 0 0 0;
        }
    }

    &__list {
        @extend %list-reset;
        margin: pxToRem(40) 0 0 0;

        @include rwd-from($breakpoint-desktop-min) {
            margin: 0 0 0 pxToRem(45);
        }
    }

    &__item {
        clear: left;
        display: inline-block;
        float: left;
        margin: 0 pxToRem(16) pxToRem(17) 0;
        position: relative;

        @include rwd-from($breakpoint-tablet-min) {
            float: none;
        }

        &:nth-child(even) {

            &::before,
            &::after {
                left: pxToRem(40);

                @include rwd-from($breakpoint-tablet-min) {
                    left: pxToRem(24);
                }

                @include rwd-from($breakpoint-desktop-min) {
                    left: pxToRem(27);
                }
            }

            #{ $self }__link {
                padding: pxToRem(17) pxToRem(28) pxToRem(17) pxToRem(68);

                @include rwd-from($breakpoint-tablet-min) {
                    padding: pxToRem(17) pxToRem(28) pxToRem(17) pxToRem(51);
                }

                @include rwd-from($breakpoint-desktop-min) {
                    padding: pxToRem(21) pxToRem(51);
                }
            }
        }

        &::before,
        &::after {
            background-color: $base-color-lead;
            bottom: 0;
            content: '';
            height: pxToRem(2);
            left: pxToRem(24);
            margin: auto 0;
            position: absolute;
            top: 0;
            width: pxToRem(17);

            @include rwd-from($breakpoint-desktop-min) {
                height: pxToRem(3);
                left: pxToRem(27);
            }
        }

        &::before {
            transform: rotate(45deg);
        }

        &::after {
            transform: rotate(-45deg);
        }
    }

    &__link {
        background-color: transparent;
        border: 1px solid #b9b9b9;
        border-radius: pxToRem(33);
        color: inherit;
        display: block;
        font-size: pxToRem(15);
        padding: pxToRem(17) pxToRem(28) pxToRem(17) pxToRem(51);
        text-decoration: none;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(20);
            padding: pxToRem(21) pxToRem(51);

            &:hover {
                border-color: $base-color-lead;
            }
        }
    }

    + .search-results__tools-right {
        position: absolute;
        right: pxToRem(35);

        @include rwd-from($breakpoint-desktop-min) {
            position: relative;
            right: auto;
        }
    }
}

.search-results__tools-title + .filter-list {
    margin: pxToRem(40) 0 0 0;

    + .search-results__tools-right {
        margin-top: pxToRem(40);

        @include rwd-from($breakpoint-desktop-min) {
            margin-top: pxToRem(50);
        }
    }
}
