.module-info {
    background-color: $module-info-bg;
    border-radius: $border-radius-global;
    margin: pxToRem(26) 0 0 0;
    padding: pxToRem(50) pxToRem(20) pxToRem(70) pxToRem(20);
    text-align: center;

    @include rwd-from($breakpoint-desktop-min) {
        margin: pxToRem(36) 0 0 0;
        padding: pxToRem(80) pxToRem(20) pxToRem(100) pxToRem(20);
    }

    &__bg {
        margin: pxToRem(5) 0;
    }

    &__title {
        color: $base-font-colour;
        font-size: pxToRem(21);
        font-weight: $font-weight-medium;
        margin: pxToRem(10) 0;

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(25);
        }
    }

    &__desc {
        font-size: pxToRem(16);
        font-weight: $font-weight-regular;
        margin: 0 auto;
        max-width: pxToRem(380);

        @include rwd-from($breakpoint-desktop-min) {
            font-size: pxToRem(18);
        }
    }
}
