.btn-toggle {
    background: none;
    border: 0;
    color: $base-color-lead;
    cursor: pointer;
    font-size: pxToRem(16);
    margin: pxToRem(28) 0 pxToRem(25) 0;
    text-decoration: underline;

    @at-root .fb-container--edu#{&} {
        color: $base-color-higher-education;
    }

    @include rwd-from($breakpoint-desktop-min) {
        &:hover {
            text-decoration: none;
        }

        &:focus {
            outline: none;
            text-shadow: 2px 2px 5px $light-gray;
        }
    }
}
